eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2013-2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define: true, graphite: true, require: true*/\ndefine(\n    \'plugin-dependencies\',\n    [\n        \'underscore\',\n        \'controllers/AuthController\',\n        \'ext/strings\'\n    ],\n    function (_, AuthController, i18nStrings) {\n        \'use strict\';\n\n        return {\n            translate: function () {\n                var test_mode     = false;\n                var original_key  = arguments[0] ? arguments[0] : null;\n                var replacements  = Array.prototype.slice.call(arguments, 1);\n\n                // simple lookup of a single key, return the key if no match found\n                var ret = original_key;\n                \n                // replace string w/ localized equivalent\n                if (i18nStrings.hasOwnProperty(original_key)) {\n                    ret = i18nStrings[original_key];\n                }\n                                      \n                ret = ret.replace(/\\\\\'/g, \'\\\'\');\n\n                // replace variables, assuming we have found the base string\n                if (ret) {\n                    _.each(replacements, function (item, index) {\n                        ret = ret.replace((\'{\' + index + \'}\'), item);\n                    });\n                }\n                if (test_mode) {\n                    return \"!!!-\" + ret + \"-!!!\";\n                }\n\n                return ret;\n            },\n\n            utils: {\n                getCurrentPage: function () {\n                    return AuthController.isLoggedIn();\n                },\n                getCurrentBasePath: function () {\n                    return \'parfait\';\n                }\n            },\n\n            config: {\n                sharedcloud_proxy_url: \"\"\n            },\n\n            auth: {\n                token: \"\"\n            },\n\n            parfait: true,\n\n            notifyUser: function (msg) {\n                graphite.events.trigger(\'load-alert\', msg);\n            }\n        };\n    }\n);\n//# sourceURL=/pluginshim/plugin-dependencies.js");

