eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2013 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, bitwise: true */\n/*global define: true, graphite: true*/\n\ndefine(\'ccweb.files.extract/models/LayerPropertiesModel\',[\n    \'jquery\',\n    \'underscore\',\n    \'backbone\',\n    \'ccweb.files.extract/Constants\',\n    \'./ColorModel\',\n    \'./TextStyleModel\',\n    \'./GradientModel\'\n], function ($, _, Backbone, Constants, ColorModel, TextStyleModel, GradientModel) {\n    \'use strict\';\n    var LayerPropertiesModel = Backbone.Model.extend({\n\n        defaults: {\n            layerType: \'\',\n            colors: null,\n            gradients: null,\n            textStyles: null\n        },\n\n        enabledGradientTypes: {\n            linear: true,\n            radial: true,\n            angle: false,\n            reflected: false, // This should be doable in CSS, we just need to mirror the stops, but disable for now\n            diamond: false,\n            shapeburst: false\n        },\n\n        enabledGradientForms: {\n            customStops: true,\n            colorNoise: false\n        },\n\n        initialize: function (attributes) {\n            this.layerType = attributes.layerType;\n        },\n\n        layerEffectsAreTransparent: function () {\n            var effects = this.get(\'layerEffects\');\n\n            if (effects && effects.solidFill && effects.solidFill.enabled) {\n                if (effects.solidFill.opacity === 100) {\n                    return false;\n                }\n            }\n            if (this.isGradientOpaque(this.getGradientEffect())) {\n                return false;\n            }\n\n            return true;\n        },\n\n        getColorsFromEffects: function (colors) {\n            var effects = this.get(\'layerEffects\'),\n                colorModel;\n\n            if (effects && effects.solidFill && effects.solidFill.enabled) {\n                colorModel = new ColorModel();\n                this.setAlpha(effects.solidFill.opacity, colorModel);\n                colors.push(colorModel.set(\n                    colorModel.parse(effects.solidFill.color, {parse: true})\n                ));\n            }\n\n            // only include base color if our layer effects are transparent\n            return this.layerEffectsAreTransparent();\n        },\n\n        getColors: function () {\n            var colors = this.get(\'colors\');\n            var blendOptions = this.get(\'blendOptions\');\n            var i;\n\n            if (colors === null) {\n                colors = [];\n                var color,\n                    shape,\n                    colorModel;\n\n                switch (this.layerType) {\n                case Constants.Type.LAYER_CONTENT:\n                    color = this.get(\'color\');\n                    shape = this.get(\'shape\');\n                    if (color) {\n                        if (this.getColorsFromEffects(colors)) {\n                            var skipFill = shape && !shape.fillEnabled;\n                            if (!skipFill) {\n                                colorModel = new ColorModel();\n                                colors.push(colorModel.set(\n                                    colorModel.parse(color, {parse: true})\n                                ));\n\n                                this.checkOpacity(blendOptions, colorModel);\n                            }\n                        }\n\n                        if (shape && shape.stroke) {\n                            var stroke = shape.stroke;\n                            if (stroke.strokeStyleContent) {\n                                var strokeContent = stroke.strokeStyleContent;\n                                color = strokeContent.color;\n                                if (color) {\n                                    colorModel = new ColorModel();\n                                    if (stroke.strokeStyleOpacity !== 100) {\n                                        this.setAlpha(stroke.strokeStyleOpacity, colorModel);\n                                    }\n                                    this.checkOpacity(blendOptions, colorModel);\n                                    colors.push(colorModel.set(\n                                        colorModel.parse(color, {parse: true})\n                                    ));\n                                }\n                            }\n                        }\n                    }\n\n                    break;\n                case Constants.Type.LAYER_TEXT:\n                    var ranges = this.get(\'textStyleRange\');\n                    if (ranges) {\n                        if (this.getColorsFromEffects(colors)) {\n                            for (i = 0; i < ranges.length; i++) {\n                                color = ranges[i].textStyle.color;\n                                if (color) {\n                                    colorModel = new ColorModel();\n                                    colors.push(colorModel.set(\n                                        colorModel.parse(color, {parse: true})\n                                    ));\n                                    this.checkOpacity(blendOptions, colorModel);\n                                }\n                            }\n                        }\n                    }\n                    break;\n                }\n            }\n            return colors;\n        },\n\n        checkOpacity: function (blendOptions, colorModel) {\n            if (blendOptions && blendOptions.opacity) {\n                this.setAlpha(blendOptions.opacity, colorModel);\n            }\n        },\n\n        setAlpha: function (opacity, colorModel) {\n            colorModel.set(\'alpha\', +(opacity / 100).toFixed(2));\n        },\n\n        getGradients: function () {\n            var gradients = this.get(\'gradients\'),\n                shape = this.get(\'shape\');\n            if (gradients === null) {\n                gradients = [];\n                if (this.layerType === Constants.Type.LAYER_CONTENT && !(shape && !shape.fillEnabled)) {\n                    var gradientEffect = this.getGradientEffect();\n                    if (gradientEffect) {\n                        gradients.push(gradientEffect);\n                    }\n                    if (this.layerEffectsAreTransparent()) {\n                        if (this.get(\'class\') === \'gradientLayer\' &&\n                                this.enabledGradientTypes[this.get(\'type\')] &&\n                                this.enabledGradientForms[this.get(\'gradient\').gradientForm]) {\n                            var gradientModel = new GradientModel(this.toJSON(), {parse: true});\n                            gradients.push(gradientModel);\n                        }\n                    }\n                }\n            }\n\n            return gradients;\n        },\n\n\n        getTextStyles: function () {\n            var textStyles = this.get(\'textStyles\');\n            var i;\n            if (textStyles === null) {\n                textStyles = [];\n                if (this.layerType === Constants.Type.LAYER_TEXT && this.get(\'rawText\') !== \'\') {\n                    var ranges = this.get(\'textStyleRange\');\n                    var textStyle, textStyleModel;\n                    if (ranges) {\n                        for (i = 0; i < ranges.length; i++) {\n                            textStyle = ranges[i].textStyle;\n                            textStyleModel = new TextStyleModel();\n                            textStyles.push(textStyleModel.set(\n                                textStyleModel.parse(textStyle, {parse: true})\n                            ));\n                        }\n                    }\n                }\n            }\n            return textStyles;\n        },\n\n\n        // “Private”\n        getGradientEffect: function () {\n            var gradientJSON, gradient, effects;\n            if (this.layerType === Constants.Type.LAYER_CONTENT) {\n                effects = this.get(\'layerEffects\');\n                if (effects) {\n                    if (effects.gradientFill && effects.gradientFill.enabled) {\n                        gradientJSON = effects.gradientFill;\n                    } else if (effects.innerGlow && effects.innerGlow.gradient && effects.innerGlow.enabled) {\n                        gradientJSON = effects.innerGlow;\n                    } else if (effects.outerGlow && effects.outerGlow.gradient && effects.outerGlow.enabled) {\n                        gradientJSON = effects.outerGlow;\n                    }\n                }\n            }\n\n            if (gradientJSON && this.enabledGradientTypes[gradientJSON.type]) {\n                gradientJSON.layerModel = this.get(\'layerModel\');\n                gradient = new GradientModel(gradientJSON, {parse: true});\n            }\n\n            return gradient;\n        },\n\n        isGradientOpaque: function (gradient) {\n            var isOpaque = gradient && gradient.get(\'enabled\') && gradient.get(\'opacity\') === 100;\n            if (isOpaque) {\n                $.each(gradient.get(\'gradient\').transparency, function (index, transparency) {\n                    if (transparency.opacity !== 100) {\n                        isOpaque = false;\n                        return;\n                    }\n                });\n            }\n\n            return isOpaque;\n        }\n    });\n\n    return LayerPropertiesModel;\n});\n\n//# sourceURL=/lib/graphite-api/public/js/ccweb.files.extract/models/LayerPropertiesModel.js");

