eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define, brackets*/\n\ndefine(\'ext/models/BracketsPreferencesModel\',[\n    \"underscore\",\n    \"backbone\",\n    \"text!ext/models/default-preferences.json\"\n], function (_, Backbone, DefaultPreferences) {\n    \"use strict\";\n\n    var DEFAULTS = JSON.parse(DefaultPreferences);\n\n    var PreferencesManager = brackets.getModule(\"preferences/PreferencesManager\");\n\n    var BracketsPreferencesModel = Backbone.Model.extend({\n        \n        defaults: {\n            preferenceSystem: PreferencesManager.stateManager.getPrefixedSystem(\"psdlens\")\n        },\n        \n        initialize: function () {\n            var self = this,\n                prefs = self.get(\"preferenceSystem\");\n\n            Object.keys(DEFAULTS).forEach(function (id) {\n                var data = DEFAULTS[id];\n                prefs.definePreference(data.key, data.type, data.initial);\n                self[id] = data.key;\n\n                if (data.type === \"object\" && (typeof data.initial === \"object\")) {\n                    // Populate missing defaults\n                    var existing = prefs.get(data.key);\n                    prefs.set(data.key, _.defaults(existing, data.initial));\n                }\n            });\n        },\n\n        restoreDefaults: function () {\n            var self = this;\n            \n            Object.keys(DEFAULTS).forEach(function (id) {\n                self.reset(id);\n            });\n        },\n        \n        reset: function (id) {\n            var data = DEFAULTS[id];\n            \n            if (!data) {\n                return;\n            }\n            \n            var prefs = this.get(\"preferenceSystem\");\n            prefs.set(data.key, data.initial);\n        },\n        \n        toJSON: function () {\n            var prefs = this.get(\"preferenceSystem\"),\n                json = {};\n\n            _.each(DEFAULTS, function (data) {\n                json[data.key] = prefs.get(data.key);\n            });\n            \n            return json;\n        },\n\n        getViewState: function (id, context) {\n            return PreferencesManager.getViewState(id, context);\n        }\n\n    });\n    \n    return new BracketsPreferencesModel();\n});\n//# sourceURL=/./models/BracketsPreferencesModel.js");

