eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2013-2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define, graphite, brackets, window*/\n\ndefine(\'override/views/landing/BracketsAssetListView\',[\n    \"jquery\",\n    \"views/landing/AssetListView\",\n    \"ext/strings\"\n], function ($, AssetListView, Strings) {\n\n    \"use strict\";\n\n    var StringUtils         = brackets.getModule(\"utils/StringUtils\");\n\n    var BracketsAssetListView = AssetListView.extend({\n        \n        $placeholder: undefined,\n        \n        initialize: function (authModel, path) {\n            this.$placeholder = $(\"#e4b-main-panel .e4b-selectionbar-placeholder\");\n            \n            AssetListView.prototype.initialize.call(this, authModel, path);\n        },\n\n        render: function () {\n            var retVal = AssetListView.prototype.render.call(this);\n\n            // HACK AssetListView doesn\'t call stopPropagation()\n            var dropTarget = $(\"#drag_drop_file_capture\")[0],\n                _onDropHandler = dropTarget.ondrop;\n\n            dropTarget.ondrop = function (event) {\n                event.stopPropagation();\n                _onDropHandler.call(dropTarget, event);\n            };\n\n            // HACK Parfait doesn\'t distinguish metrics here where the view\n            // shows the user\'s files vs. showing the upload drop target\n            if (!this.loading) {\n                if (this.collection.models.length) {\n                    graphite.events.trigger(\"browse\");\n                } else {\n                    graphite.events.trigger(\"browse-show-upload-files\");\n                }\n            }\n            \n            var self = this;\n            if (this.$placeholder.find(\"#al-refresh-button\").length === 0) {\n                var $refreshButton = $(\'<div id=\"al-refresh-button\"/>\');\n                $refreshButton.click(function () {\n                    $refreshButton.addClass(\"spinning\");\n                    self.reRender();\n                });\n                this.$placeholder.prepend($refreshButton);\n            }\n\n            return retVal;\n        },\n\n        onAssetCollectionError: function (response) {\n            this.scErrorText = StringUtils.format(\n                Strings.ERROR_ASSET_LIST_MESSAGE,\n                StringUtils.breakableUrl(this.collection.path)\n            );\n\n            AssetListView.prototype.onAssetCollectionError.call(this, response);\n        },\n        \n        onReset: function () {\n            this.$placeholder.find(\"#al-refresh-button\").removeClass(\"spinning\");\n            \n            AssetListView.prototype.onReset.call(this);\n        },\n        \n        remove: function () {\n            this.$placeholder.find(\"#al-refresh-button\").remove();\n            \n            return AssetListView.prototype.remove.call(this);\n        }\n    });\n\n    return BracketsAssetListView;\n});\n\n//# sourceURL=/./views/landing/BracketsAssetListView.js");

