eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, bitwise: true */\n/*global define, brackets, PathUtils*/\ndefine(\'ext/ims/IMSProxy\',[\"ext/NodeAPI\"], function (NodeAPI) {\n    \"use strict\";\n\n    var _proxyDomain,\n        configurePromise,\n        nodeDomain = NodeAPI.nodeDomain;\n\n    return {\n        configure: function () {\n            if (!configurePromise) {\n                configurePromise = nodeDomain.exec(\"configureIMS\").then(function (res) {\n                    _proxyDomain = \"http://127.0.0.1:\" + res.port;\n                }, function (err) {\n                    console.error(err);\n                });\n            }\n\n            return configurePromise;\n        },\n\n        proxyUrlInfo: function (targetURL) {\n            var targetDomain = PathUtils.parseUrl(targetURL).domain;\n\n            return {\n                domain: targetDomain,\n                url: targetURL.replace(targetDomain, _proxyDomain)\n            };\n        },\n\n        setCookie: function (cookieString) {\n            nodeDomain.exec(\"setCookie\", cookieString);\n        }\n    };\n});\n//# sourceURL=/./ims/IMSProxy.js");

