eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, bitwise: true */\n/*global define, brackets*/\n\ndefine(\'override/models/BracketsAuthModel\',[\n    \'jquery\',\n    \'config/environment\',\n    \'models/AuthModel\',\n    \'ext/CacheManager\',\n    \'ext/ims/IMSLibWrapper\',\n    \'ext/models/BracketsPreferencesModel\',\n    \'ext/ims/IMSLib\'\n], function ($, environment, AuthModel, CacheManager, IMSLibWrapper, BracketsPreferencesModel, ImsLib) {\n    \"use strict\";\n    \n    var prefs               = BracketsPreferencesModel.get(\"preferenceSystem\");\n    \n    var BracketsAuthModel = AuthModel.extend({\n\n        initialize: function () {\n            var self = this;\n\n            // Override AuthModel throttlecheck\n            this.throttled = false;\n            \n            function updateData(userData) {\n                self.user_data = {};\n                self.user_data.accountEnabled = true;\n                self.user_data.analyticsOptIn = false;\n                self.user_data.email = userData.email;\n                self.user_data.serviceAccounts = userData.serviceAccounts;\n                self.adobeID = userData.email;\n            }\n            \n            $(IMSLibWrapper).on(\"signIn\", function (event, userData) {\n                updateData(userData);\n            }).on(\"signOut\", function (event) {\n                self.invalidate();\n            });\n            \n            $(ImsLib).on(\"signInAuto\", function (event, userData) {\n                updateData(userData.authorizedUser);\n            }).on(\"signOut\", function (event) {\n                self.invalidate();\n            });\n        },\n        configure: function () {\n            return IMSLibWrapper.getAccessToken();\n        },\n        \n        isPaidAccount: function () {\n            // FIXME save in preference so we can track this offline\n            var accounts = this.user_data && this.user_data.serviceAccounts;\n            \n            if (!accounts || !Array.isArray(accounts)) {\n                return false;\n            }\n            \n            return accounts.some(function (account) {\n                return account.serviceCode === \"creative_cloud\" && account.serviceLevel === \"CS_LVL_2\";\n            });\n        },\n\n        isValid: function () {\n            // HACK https://git.corp.adobe.com/jhagenst/brackets-parfait-extension/issues/548\n            // If the user starts Brackets offline, imslib.js can\'t load and\n            // won\'t populate AuthModel.user_data used here in AuthModel.isValid().\n            // If the user starts Brackets online and logged in (expired or not),\n            // we can at least show what we have in the cache. In the long run\n            // this isn\'t the ideal fix and we should at least change usage of\n            // AuthController.isLoggedIn() in AssetView and AssetListView\n            if (!CacheManager.isEmpty()) {\n                return true;\n            }\n\n            return AuthModel.prototype.isValid.call(this);\n        },\n        \n        // Need to override the default method in prototype to avoid\n        // dependency on invite code. Expecting upstream changes.\n        // Eventually we may need to revisit, looks\n        // like Parfait is not fully using this feature yet.\n        isValidWithUserFeature: function () {\n            return true;\n        },\n\n        isAnalyticsOptIn: function () {\n            return prefs.get(BracketsPreferencesModel.METRICS_ENABLED);\n        },\n\n        setAnalyticsOptIn: function (bAnalyticsOptIn) {\n            // do nothing\n        },\n\n        // Override super method that responds synchronously with\n        // attributes.access_token and instead use IMSLib\n        getAccessToken: function () {\n            var deferred = new $.Deferred();\n            IMSLibWrapper.getAccessToken().done(function (accessToken) {\n                deferred.resolve(accessToken);\n            });\n            return deferred.promise();\n        }\n    });\n\n    return BracketsAuthModel;\n});\n//# sourceURL=/./models/BracketsAuthModel.js");

