eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define: true, graphite: true, window: true, document: true*/\n\ndefine(\'override/views/detail/BracketsZoomView\',[\n    \'jquery\',\n    \'underscore\',\n    \'backbone\',\n    \'ccweb.files.extract/utils/TemplateUtil\',\n    \'ccweb.files.extract/controllers/ZoomController\',\n    \'ccweb.files.extract/controllers/SelectionController\',\n    \'text!ccweb.files.extract/views/templates/zoomControlsTemplate.html\'\n], function ($, _, Backbone, TemplateUtil, ZoomController, SelectionController, ZoomControlsTemplate) {\n    \'use strict\';\n    var BracketsZoomView = Backbone.View.extend({\n\n        className: \'zoom-controls\',\n\n        events: {\n            \'click .zoom-100-button\': \'handleOneToOne\',\n            \'click .zoom-fit-button\': \'handleZoomToFit\',\n            \'click .zoom-input\': \'handleZoomInputClick\',\n            \'focus .zoom-input\': \'handleFocus\',\n            \'keydown .zoom-input\': \'handleInputKey\',\n            \'keypress .zoom-input\': \'handleKeyPress\',\n            \'blur .zoom-input\': \'handleBlur\'\n        },\n\n        initialize: function () {\n            _.bindAll(this, \'handleDocumentKey\', \'toggleZoomButtonStates\');\n            this.render();\n            graphite.events.on(\'zoomChanged\', this.handleZoomChange, this);\n            graphite.events.on(\'zoomToFit\', this.handleZoomToFit, this);\n            graphite.events.on(\'drawPreviewFinish\', this.toggleZoomButtonStates, this);\n            graphite.events.on(\'psdModelInfoChanged\', this.render, this);\n            $(document).on(\'keydown.zoomview\', this.handleDocumentKey);\n            $(window).on(\'resize.zoomView\', _.debounce(this.toggleZoomButtonStates, 300));\n        },\n\n        render: function () {\n            this.$el.html(TemplateUtil.createTemplate(ZoomControlsTemplate, SelectionController.getPSDModel().toJSON()));\n            this.$zoomInput = this.$el.find(\'.zoom-input\');\n            this.setInputLabel();\n            return this;\n        },\n\n        remove: function () {\n            $(document).off(\'keydown.zoomview\', this.handleDocumentKey);\n            $(window).off(\'.zoomView\');\n            graphite.events.off(null, null, this);\n            this.$zoomInput = null;\n            this.$el.empty();\n            return Backbone.View.prototype.remove.call(this);\n        },\n\n        setInputLabel: function () {\n            this.$zoomInput.text(ZoomController.getZoomPercent());\n        },\n\n        handleZoomInputClick: function () {\n            if (!this.editingZoom) {\n                this.startEditing();\n            }\n        },\n\n        handleZoomToFit: function () {\n            var psdModel = SelectionController.getPSDModel(),\n                imageBounds = psdModel.get(\'imgdata\').bounds,\n                $psdPreviewView = $(\'.psd-preview-view\'),\n                viewWidth = $psdPreviewView.outerWidth() - 50,\n                imageWidth = imageBounds.right - imageBounds.left,\n                zoomLevel = viewWidth / imageWidth;\n\n            zoomLevel = zoomLevel > 1.0 || viewWidth <= 0 ? 1.0 : zoomLevel;\n            ZoomController.setZoomLevel(zoomLevel);\n        },\n\n        handleOneToOne: function () {\n            ZoomController.setZoomLevel(1);\n        },\n\n        handleZoomChange: function () {\n            this.setInputLabel();\n            this.toggleZoomButtonStates();\n        },\n\n        toggleZoomButtonStates: function () {\n            var $fitButton = this.$el.find(\'.zoom-fit-button\'),\n                $zoom100Button = this.$el.find(\'.zoom-100-button\'),\n                zoomLevel = ZoomController.getZoomLevel(),\n                psdModel = SelectionController.getPSDModel(),\n                imageBounds = psdModel.get(\'imgdata\').bounds,\n                $psdPreviewView = $(\'.psd-preview-view\'),\n                viewWidth = $psdPreviewView.outerWidth() - 50,\n                imageWidth = imageBounds.right - imageBounds.left,\n                fitZoomLevel = viewWidth / (imageWidth * zoomLevel);\n\n            if ($psdPreviewView.length && $psdPreviewView.outerWidth()) {\n                $fitButton.toggleClass(\"disabled\", fitZoomLevel === zoomLevel || fitZoomLevel >= 1);\n                $zoom100Button.toggleClass(\"disabled\", zoomLevel === 1.0);\n            }\n        },\n\n        handleInputKey: function (event) {\n            var keyCode = event.keyCode;\n            if (this.editingZoom) {\n                if (keyCode === 27) {\n                    this.stopEditing();\n                } else if (keyCode === 9 ||\n                        keyCode === 13 ||\n                        keyCode === 14) {\n                    event.stopImmediatePropagation();\n                    event.preventDefault();\n                    this.commitEdit();\n                }\n            }\n        },\n        \n        handleKeyPress: function (event) {\n            // limit the length of text that can be entered\n            return (this.$zoomInput[0].innerText.length <= 8);\n        },\n\n        handleDocumentKey: function (event) {\n            var keyCode = event.keyCode;\n            if (event.altKey) {\n                if (keyCode === 189 || keyCode === 109 || keyCode === 173) {\n                    event.stopPropagation();\n                    event.preventDefault();\n                    ZoomController.zoomOut();\n                } else if (keyCode === 187 || keyCode === 107 || keyCode === 61) {\n                    event.stopPropagation();\n                    event.preventDefault();\n                    ZoomController.zoomIn();\n                }\n            }\n        },\n\n        handleBlur: function () {\n            this.setInputLabel();\n            this.stopEditing();\n        },\n\n        startEditing: function () {\n            this.revertVal = this.$zoomInput.text();\n            this.$zoomInput.attr(\"contenteditable\", true);\n            this.editingZoom = true;\n            this.$zoomInput[0].ownerDocument.defaultView.getSelection().selectAllChildren(this.$zoomInput[0]);\n            this.$zoomInput.addClass(\"editing\");\n            this.$zoomInput.focus();\n        },\n\n        stopEditing: function () {\n            this.$zoomInput.attr(\"contenteditable\", false);\n            this.editingZoom = false;\n            this.$zoomInput.removeClass(\"editing\");\n        },\n\n        commitEdit: function () {\n            var value = parseFloat(this.$zoomInput.text()) / 100.0,\n                zoom = (!isNaN(value) && value > 0) ? value : ZoomController.getZoomLevel();\n            this.stopEditing();\n            ZoomController.setZoomLevel(zoom);\n        }\n\n    });\n    return BracketsZoomView;\n});\n\n//# sourceURL=/./views/detail/BracketsZoomView.js");

