eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define: true, brackets: true*/\n\ndefine(\'ext/views/CalloutView\',[\n    \'jquery\',\n    \'underscore\',\n    \'backbone\',\n    \'text!ext/views/templates/callout-view.html\',\n    \'ext/strings\'\n], function ($, _, Backbone, CalloutViewTemplate, Strings) {\n    \"use strict\";\n\n    var AnimationUtils  = brackets.getModule(\"utils/AnimationUtils\"),\n        KeyEvent        = brackets.getModule(\"utils/KeyEvent\"),\n        ViewUtils       = brackets.getModule(\"utils/ViewUtils\");\n\n    var CalloutView = Backbone.View.extend({\n\n        templateId: null,\n        disableRemoveAnimation: false,\n\n        /**\n         * click .dismiss-button: trigger \"dismiss\" event\n         * click: remove callout from DOM\n         */\n        events: {\n            // FIXME https://git.corp.adobe.com/jhagenst/brackets-parfait-extension/issues/840\n            // Disable implicit removal, forcing user to dismiss callouts explicitly\n            //\"click\": \"remove\",\n            \"click .dismiss-button\": \"_handleDismiss\"\n        },\n\n        initialize: function (options) {\n            this.$targetElement = options.$targetElement;  // either a DOM element *or* an object with left/right/top/bottom fields\n            this.$viewport = options.$viewport;  // optional\n            this.templateId = options.templateId;\n            this.disableRemoveAnimation = options.disableRemoveAnimation || false;\n            this.dismissLabel = options.dismissLabel || Strings.GOT_IT;\n\n            this.render();\n        },\n\n        createTemplate: function () {\n            var srcHTML = $(CalloutViewTemplate).filter(this.templateId).html(),\n                template = _.template(srcHTML);\n\n            return template(_.extend({ dismissLabel: this.dismissLabel }, Strings)).trim();\n        },\n\n        render: function () {\n            this.$el.empty();\n            \n            // Adapted from Editor.displayErrorMessageAtCursor()\n            // Omits blur, change, cursorActivity, and update event handling\n            // Omits close animation\n            var arrowBelow, targetCoord, popoverRect,\n                top, left, arrowLeft,\n                $messagePopover,\n                POPOVER_MARGIN = 10,\n                POPOVER_ARROW_HALF_WIDTH = 10,\n                POPOVER_ARROW_HALF_BASE = POPOVER_ARROW_HALF_WIDTH + 3; // 3 is border radius\n            \n            targetCoord = this._findTargetPos();\n            this.targetCoord = targetCoord;  // save for later checks in updatePosition()\n            \n            // Determine if arrow is above or below\n            // Assume popover height is max of 2 lines\n            arrowBelow = (targetCoord.top > 100);\n            \n            // Text is dynamic, so build popover first so we can measure final width\n            $messagePopover = this.$el.addClass(\"e4b popover-message\").appendTo($(\"body\"));\n            if (!arrowBelow) {\n                $(\"<div/>\").addClass(\"arrowAbove\").appendTo($messagePopover);\n                this.$el.addClass(\"arrowAbove\");\n            }\n            $(\"<div/>\").addClass(\"text\").appendTo($messagePopover).html(this.createTemplate());\n            if (arrowBelow) {\n                $(\"<div/>\").addClass(\"arrowBelow\").appendTo($messagePopover);\n                this.$el.addClass(\"arrowBelow\");\n            }\n            \n            // Estimate where to position popover.\n            top = (arrowBelow) ? targetCoord.top - $messagePopover.height() - POPOVER_MARGIN\n                               : targetCoord.bottom + POPOVER_MARGIN;\n\n            // Horizontally center on target\n            var targetWidth = targetCoord.right - targetCoord.left;\n            left = targetCoord.left + ((targetWidth - $messagePopover.width()) / 2);\n            \n            popoverRect = {\n                top:    top,\n                left:   left,\n                height: $messagePopover.height(),\n                width:  $messagePopover.width()\n            };\n            \n            // Popover text and arrow are positioned individually\n            $messagePopover.css({\"top\": top, \"left\": left});\n            \n            // Position popover arrow centered\n            arrowLeft = ($messagePopover.width() / 2) - POPOVER_ARROW_HALF_WIDTH;\n\n            if (arrowBelow) {\n                $messagePopover.find(\".arrowBelow\").css({\"margin-left\": arrowLeft});\n            } else {\n                $messagePopover.find(\".arrowAbove\").css({\"margin-left\": arrowLeft});\n            }\n\n            // Animate open\n            AnimationUtils.animateUsingClass($messagePopover[0], \"animateOpen\").done(function () {\n                // Make sure we still have a popover\n                if ($messagePopover && $messagePopover.length > 0) {\n                    $messagePopover.addClass(\"open\");\n                }\n            });\n\n            return this;\n        },\n        \n        _findTargetPos: function () {\n            var targetCoord;\n            if (this.$targetElement.jquery) {\n                targetCoord = this.$targetElement.offset();\n                targetCoord.bottom = targetCoord.top + this.$targetElement.height();\n                targetCoord.right = targetCoord.left + this.$targetElement.width();\n            } else {\n                // Handle explicit coordinates\n                targetCoord = this.$targetElement;\n            }\n            \n            if (this.$viewport) {\n                // If a viewport was provided, make sure the target is visible within it. If not, clip pos\n                // to the closest viewport edge.\n                var EDGE_THRESHOLD = 15;\n                var viewportBounds = this.$viewport.offset();\n                viewportBounds.bottom = viewportBounds.top + this.$viewport.height();\n                viewportBounds.right = viewportBounds.left + this.$viewport.width();\n                \n                if (targetCoord.right - viewportBounds.left < EDGE_THRESHOLD) {\n                    targetCoord.left = viewportBounds.left;\n                    targetCoord.right = targetCoord.left;\n                } else if (targetCoord.left - viewportBounds.right > -EDGE_THRESHOLD) {\n                    targetCoord.left = viewportBounds.right;\n                    targetCoord.right = targetCoord.left;\n                }\n                \n                if (targetCoord.bottom - viewportBounds.top < EDGE_THRESHOLD) {\n                    targetCoord.top = viewportBounds.top;\n                    targetCoord.bottom = targetCoord.top;\n                } else if (targetCoord.top - viewportBounds.bottom > -EDGE_THRESHOLD) {\n                    targetCoord.top = viewportBounds.bottom;\n                    targetCoord.bottom = targetCoord.top;\n                }\n            }\n            \n            return targetCoord;\n        },\n        \n        updatePosition: function () {\n            if (!this.targetCoord) {\n                // If we haven\'t finished our first render() yet, no need to do anything here\n                return;\n            }\n            \n            var currentTargetPos = this._findTargetPos();\n            if (currentTargetPos.left !== this.targetCoord.left || currentTargetPos.right !== this.targetCoord.right ||\n                    currentTargetPos.top !== this.targetCoord.top || currentTargetPos.bottom !== this.targetCoord.bottom) {\n                // Avoid re-rendering unless our position has really changed, since a spurious re-render in mid-click can\n                // prevent the click event from firing (mousedown/mouseup occur on differing DOM nodes)\n                this.render();\n            }\n        },\n\n        /**\n         * @private\n         * Triggered when .dismiss-button is clicked. Note that callout is\n         * removed from DOM via click handler for $el.\n         */\n        _handleDismiss: function () {\n            this.trigger(\"dismiss\");\n        },\n\n        remove: function () {\n            var self = this,\n                doRemove = function () {\n                    var $messagePopover = self.$el;\n                    $messagePopover.remove();\n                    $messagePopover = null;\n                };\n\n            if (this.disableRemoveAnimation) {\n                doRemove();\n            } else {\n                AnimationUtils.animateUsingClass(this.el, \"animateClose\").always(doRemove);\n            }\n\n            this.trigger(\"remove\");\n        }\n    });\n\n    return CalloutView;\n\n});\n//# sourceURL=/./views/CalloutView.js");

