eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2013 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, bitwise: true */\n/*global define: true, graphite: true, document: true, mixpanel: true, window: true*/\n\ndefine(\'controllers/AuthController\',[\n    \'jquery\',\n    \'underscore\',\n    \'backbone\',\n    \'config/environment\'\n], function ($, _, Backbone, environment) {\n    \'use strict\';\n\n    // serverAPI - ccweb should handle authentication\n    var IMS_REDIRECT_URI = window.location.protocol + \'//\' + window.location.host,\n        AuthController = Backbone.Model.extend({\n            authModel: null,\n\n            getLoginURL: function () {\n                var loginURL = environment.getImsAuthEndpoint_Login() + \'?client_id=\' + environment.IMS_CLIENT_ID + \'&redirect_uri=\' + IMS_REDIRECT_URI + \'/api/v1/login&scope=openid,AdobeID,creative_cloud&dc=\' + !!window.graphite.isFeatureEnabled(\'aug21\');\n                return loginURL;\n            },\n\n            getLogoutURL: function () {\n                return environment.getImsAuthEndpoint_Logout() + \'?redirect_uri=\' + IMS_REDIRECT_URI + \'/api/v1/logout&client_id=\' + environment.IMS_CLIENT_ID + \'&access_token=null\';\n            },\n\n            setAuthModel: function (model) {\n                this.authModel = model;\n            },\n\n            getAuthModel: function () {\n                return this.authModel;\n            },\n\n            setCookie: function (name, value, hours) {\n                var expires = \'\';\n\n                if (hours) {\n                    var date = new Date();\n                    date.setTime(date.getTime() + (hours * 60 * 60 * 1000));\n                    expires = \'; expires=\' + date.toGMTString();\n                }\n                document.cookie = name + \'=\' + value + expires + \'; path=/\';\n            },\n\n            getCookie: function (name) {\n                var cookies = document.cookie.split(\';\'),\n                    i,\n                    cookie;\n                for (i in cookies) {\n                    if (cookies.hasOwnProperty(i)) {\n                        cookie = cookies[i].trim().split(\'=\');\n                        cookie[0].trim();\n                        if (cookie[0] === name) {\n                            return cookie[1];\n                        }\n                    }\n                }\n                return;\n            },\n\n            login: function () {\n                if (this.authModel.isValid()) {\n                    return;\n                }\n\n                graphite.events.trigger(\'load-login-page\', this.getLoginURL());\n            },\n\n            logout: function () {\n                if (!this.authModel.isValid()) {\n                    return;\n                }\n\n                document.location = this.getLogoutURL();\n            },\n\n            reauthenticate: function () {\n                this.authModel.invalidate();\n                this.login();\n            },\n\n            isLoggedIn: function () {\n                return this.authModel && this.authModel.isValid();\n            }\n\n        });\n\n    return new AuthController();\n});\n\n//# sourceURL=/lib/graphite-api/public/js/controllers/AuthController.js");

