eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2013-2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define: true, graphite: true*/\n\ndefine(\'ccweb.files.extract/views/assets/DerivedAssetView\',[\n    \'jquery\',\n    \'underscore\',\n    \'backbone\',\n    \'ccweb.files.extract/controllers/SelectionController\',\n    \'ccweb.files.extract/controllers/DetailsController\',\n    \'ccweb.files.extract/models/LayerModelMap\',\n    \'ccweb.files.extract/utils/ImageUtil\',\n    \'ccweb.files.extract/utils/TemplateUtil\',\n    \'text!ccweb.files.extract/views/templates/derivedAssetListItemTemplate.html\'\n], function ($, _, Backbone, SelectionController, DetailsController, LayerModelMap, ImageUtil,\n             TemplateUtil, DerivedAssetListItemTemplate) {\n    \'use strict\';\n\n    var InspectDerivedAssetView = Backbone.View.extend({\n        events: {\n            \'load img\': \'handleImageLoad\',\n            \'mouseenter\': \'handleMouseEnter\',\n            \'mouseleave\': \'handleMouseLeave\',\n            \'click .del-button\': \'handleDeleteButton\'\n        },\n\n        initialize: function () {\n            this.render();\n            this.addHandlers();\n        },\n\n        render: function () {\n            this.setElement(TemplateUtil.createTemplate(DerivedAssetListItemTemplate, this.model.attributes));\n            this.$deleteButton = this.$el.find(\'.del-button\');\n        },\n\n\n        addHandlers: function () {\n            var self = this;\n\n            this.$el.click(function (event) {\n                event.preventDefault();\n                if (SelectionController.get(\'inspectedAsset\') &&\n                        SelectionController.get(\'inspectedAsset\').get(\'id\') === self.model.get(\'id\')) {\n                    DetailsController.setInspectedAsset(null);\n                } else {\n                    DetailsController.setInspectedAsset(self.model);\n                }\n                //initial metric tracking for when downloading the extracted asset\n                graphite.events.trigger(\'downloading-extracted-asset\');\n                if (!self.$downloadFrame) {\n                    self.$downloadFrame = $(\'<iframe class=\"download-frame\"/>\');\n                    self.$el.append(self.$downloadFrame);\n                }\n\n                // Ensure resource is accessible before ultimately downloading.\n                var assetURL = graphite.serverAPI.getDerivedDownloadURL(self.model.get(\'guid\'), self.model.get(\'name\'));\n                graphite.serverAPI.getAssetETag(self.model.get(\'guid\'),\n                    function (response) {\n                        self.$downloadFrame.attr(\'src\', assetURL);\n                    },\n                    function (response) {\n                        if (response.status === 401) {\n                            graphite.events.trigger(\'reauthenticate\');\n                        }\n                    },\n                    this);\n            });\n        },\n\n        removeEventListeners: function () {\n            DetailsController.off(null, null, this);\n        },\n\n        //------------------------------------------------\n        // Handlers\n        //------------------------------------------------\n\n        handleImageLoad: function (event) {\n            var image = event.target;\n            var wrapper = $(image).parent();\n            ImageUtil.sizeAndCenterImage(image,\n                parseInt(wrapper.width(), 10),\n                parseInt(wrapper.height(), 10),\n                ImageUtil.ImageScaleType.FIT,\n                false);\n        },\n\n        handleMouseEnter: function () {\n            this.$deleteButton.css(\'visibility\', \'visible\');\n        },\n\n        handleMouseLeave: function () {\n            this.$deleteButton.css(\'visibility\', \'hidden\');\n        },\n\n        handleDeleteButton: function (event) {\n            this.$el.addClass(\'pending-delete\');\n            graphite.serverAPI.deleteDerivedAsset(this.model.get(\'guid\'),\n                this.handleDeleteSuccess,\n                this.handleDeleteError,\n                this);\n            event.stopImmediatePropagation();\n        },\n\n        handleDeleteSuccess: function () {\n            var collection = this.model.collection;\n            collection.remove(this.model);\n        },\n\n        handleDeleteError: function (result) {\n            if (result && result.status === 401) {\n                graphite.events.trigger(\'reauthenticate\');\n            }\n            this.$el.removeClass(\'pending-delete\');\n        },\n\n        remove: function () {\n            if (this.$downloadFrame) {\n                this.$downloadFrame.remove();\n                this.$downloadFrame = null;\n            }\n            this.$deleteButton = null;\n            this.removeEventListeners();\n            return Backbone.View.prototype.remove.call(this);\n        }\n    });\n\n    return InspectDerivedAssetView;\n});\n\n//# sourceURL=/lib/graphite-api/public/js/ccweb.files.extract/views/assets/DerivedAssetView.js");

