eval("/*\n * Copyright (c) 2015 Adobe Systems Incorporated. All rights reserved.\n *\n * Permission is hereby granted, free of charge, to any person obtaining a\n * copy of this software and associated documentation files (the \"Software\"),\n * to deal in the Software without restriction, including without limitation\n * the rights to use, copy, modify, merge, publish, distribute, sublicense,\n * and/or sell copies of the Software, and to permit persons to whom the\n * Software is furnished to do so, subject to the following conditions:\n *\n * The above copyright notice and this permission notice shall be included in\n * all copies or substantial portions of the Software.\n *\n * THE SOFTWARE IS PROVIDED \"AS IS\", WITHOUT WARRANTY OF ANY KIND, EXPRESS OR\n * IMPLIED, INCLUDING BUT NOT LIMITED TO THE WARRANTIES OF MERCHANTABILITY,\n * FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT. IN NO EVENT SHALL THE\n * AUTHORS OR COPYRIGHT HOLDERS BE LIABLE FOR ANY CLAIM, DAMAGES OR OTHER\n * LIABILITY, WHETHER IN AN ACTION OF CONTRACT, TORT OR OTHERWISE, ARISING\n * FROM, OUT OF OR IN CONNECTION WITH THE SOFTWARE OR THE USE OR OTHER\n * DEALINGS IN THE SOFTWARE.\n *\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, maxerr: 50 */\n/*global $, define, brackets, Mustache, graphite */\n\ndefine(\'ext/ims/IMSLib\',[\'require\',\'exports\',\'module\',\'ext/command/Commands\',\'ext/strings\',\'controllers/AuthController\',\'ext/models/ExtensionMetaData\',\'ext/models/BracketsPreferencesModel\'],function (require, exports, module) {\n    \"use strict\";\n\n    var Commands                    = require(\"ext/command/Commands\"),\n        Strings                     = require(\"ext/strings\"),\n        AuthController              = require(\"controllers/AuthController\"),\n        ExtensionMetaData           = require(\"ext/models/ExtensionMetaData\"),\n        BracketsPreferencesModel    = require(\"ext/models/BracketsPreferencesModel\");\n    \n    var AppInit                     = brackets.getModule(\"utils/AppInit\"),\n        StringUtils                 = brackets.getModule(\"utils/StringUtils\"),\n        HealthLogger;\n\n    // cached authorization status from the shell\n    var authStatusCache = null,\n        authStatusDeferred = null,\n        authStatusTimer = null,\n        authCallTimer = null;\n    \n    var prefs = BracketsPreferencesModel.get(\"preferenceSystem\");\n        // error codes from the shell\n    var IMS_NO_ERROR = 0,\n        IMS_ERR_CALL_PENDING = 17,\n        IMS_ERR_EXPIRED_ACCESS_TOKEN = 18;\n    \n        // max retries when the error shell error is CALL_PENDING\n    var MAX_RETRIES                 = 10,\n        MS_WAIT_UNTIL_RETRY         = 100,\n        WAIT_TIMEOUT                = 10000,\n        HEALTH_DATA_LOGGING_VERSION = \"1.4.0\";\n\n    var thorLoggedOut = false;\n        /**\n     * Clear cached auth status information\n     */\n    function invalidateCache() {\n        authStatusCache = null;\n        authStatusTimer = null;\n    }\n    \n        \n    /**\n     * Make an authorized user object from the status object returned from the\n     * shell. For now just strips the access token-related information from the\n     * status object.\n     * \n     * @param {Object} status - IMSLib status object\n     * @return {Object} - authorized user object\n     */\n    function getAuthorizedUserFromStatus(status) {\n        // make shallow copy. This works only with key value pairs where\n        // the value is not a function\n        var modifiedUserProfileJson = JSON.parse(JSON.stringify(status));\n\n        delete modifiedUserProfileJson.access_token;\n        delete modifiedUserProfileJson.expires_in;\n        delete modifiedUserProfileJson.refresh_token;\n        delete modifiedUserProfileJson.token_type;\n\n        return modifiedUserProfileJson;\n    }\n    \n    /**\n     * Asynchronously get an authStatus object, either from the cache or from the shell.\n     *\n     * @param {boolean} forceRefresh - if set, ignore the cached status and force a refresh\n     * @returns {$.Promise<{accessToken: string, authorizedUser: Object}>} - an auth status object\n     */\n    function _getAuthStatus(forceRefresh) {\n        var deferred;\n        \n        function getAuthStatusHelper(retryCount) {\n            \n            if (retryCount > MAX_RETRIES) {\n                deferred.reject();\n            }\n            \n            if (!brackets.app.getAuthorizedUser) {\n                deferred.reject();\n            } else {\n                brackets.app.getAuthorizedUser(function (err, status) {\n                    \n                    if (err === IMS_NO_ERROR && status && authStatusDeferred) {\n                    \n                        try {\n                            var statusObj = JSON.parse(status),\n                                accessToken = statusObj.access_token,\n                                authorizedUser = getAuthorizedUserFromStatus(statusObj),\n                                expiresIn = statusObj.expires_in;\n                                \n                                // Decode the access token payload\n                            if (accessToken) {\n                                var encodedPayload = accessToken.slice(\n                                    accessToken.indexOf(\".\") + 1,\n                                    accessToken.lastIndexOf(\".\")\n                                );\n                                \n                                var decodedPayload = window.atob(encodedPayload);\n                                var tokenPayload = JSON.parse(decodedPayload);\n                            // Validate the access token against the expiry field in the payload\n                                var utcExpiresAt = parseInt(tokenPayload.created_at, 10) +\n                                    parseInt(tokenPayload.expires_in, 10);\n                                if ((Date.now() < utcExpiresAt)) {\n                                    // Valid access token\n\n                                    // cache the auth status\n                                    authStatusCache = {\n                                        accessToken: accessToken,\n                                        authorizedUser: authorizedUser\n                                    };\n\n                                    // invalidate and refresh the cached status once the token expires\n                                    authStatusTimer = window.setTimeout(function () {\n                                        $(exports).triggerHandler(\"signOut\");\n                                        invalidateCache();\n                                    }, expiresIn);\n                                    AuthController.getAuthModel().attributes.access_token = accessToken;\n                                    $(exports).triggerHandler(\"signInAuto\", authStatusCache);\n                                    var cmdToggleAuth = Commands.get(Commands.TOGGLE_AUTH);\n                                    cmdToggleAuth.setName(StringUtils.format(Strings.MENU_CC_SIGN_OUT, authStatusCache.authorizedUser.email));\n                                    deferred.resolve(authStatusCache);\n                                } else {\n                                    $(exports).triggerHandler(\"signOut\");\n                                    invalidateCache();\n                                    deferred.reject(IMS_ERR_EXPIRED_ACCESS_TOKEN);\n                                }\n                            } else {\n                                deferred.reject();\n                            }\n\n                        } catch (parseError) {\n                            console.error(\"Unable to parse auth status: \", parseError);\n                            deferred.reject(parseError);\n                        }\n                    } else if (err === IMS_ERR_CALL_PENDING) {\n                        window.setTimeout(function () {\n                            ++retryCount;\n                            getAuthStatusHelper(retryCount);\n                        }, MS_WAIT_UNTIL_RETRY);\n                    } else {\n                        deferred.reject();\n                    }\n                });\n            }\n        }\n        \n        if (authStatusDeferred) {\n            deferred = authStatusDeferred;\n        } else {\n            deferred = new $.Deferred();\n            authStatusDeferred = deferred;\n        }\n        \n        if (!authCallTimer) {\n            authCallTimer = window.setTimeout(function () {\n                deferred.reject();\n            }, WAIT_TIMEOUT);\n        }\n        \n        deferred.always(function () {\n            window.clearInterval(authCallTimer);\n            authStatusDeferred = null;\n            authCallTimer = null;\n        });\n        \n        if (!authStatusCache || forceRefresh) {\n            getAuthStatusHelper();\n        } else {\n            deferred.resolve(authStatusCache);\n        }\n\n        return deferred.promise();\n    }\n\n    function isThorLoggedOut() {\n        return thorLoggedOut;\n    }\n\n    function isUserSignedIn() {\n        return !!authStatusCache;\n    }\n\n    function signOut() {\n        invalidateCache();\n        thorLoggedOut = true;\n        var cmdToggleAuth = Commands.get(Commands.TOGGLE_AUTH);\n        cmdToggleAuth.setName(Strings.MENU_CC_SIGN_IN);\n        $(exports).triggerHandler(\"signOut\");\n    }\n\n    function getAccessToken() {\n        var deferred = new $.Deferred();\n        if (thorLoggedOut) {\n            deferred.reject();\n        } else {\n            _getAuthStatus().done(function (status) {\n                deferred.resolve(status);\n            }).fail(function (err) {\n                deferred.reject();\n            });\n        }\n        return deferred.promise();\n    }\n    \n    function logUserInfo() {\n        function _logHelper() {\n            var data = {};\n            ExtensionMetaData.getAcceptedEULA().done(function () {\n                if (brackets.app.getAuthorizedUser) {\n                    brackets.app.getAuthorizedUser(function (err, status) {\n                        if (err === IMS_NO_ERROR && status) {\n                            data.userAutoSignedIn = true;\n                            var statusObj = JSON.parse(status),\n                                authorizedUser = getAuthorizedUserFromStatus(statusObj),\n                                accounts = authorizedUser.serviceAccounts;\n\n                            if (accounts && Array.isArray(accounts)) {\n                                data.isCCSubscribedUser = accounts.some(function (account) {\n                                    return account.serviceCode === \"creative_cloud\" && account.serviceLevel === \"CS_LVL_2\";\n                                });\n                            }\n                        } else {\n                            data.userAutoSignedIn = false;\n                        }\n                        HealthLogger.setHealthDataLog(\"e4b\", data);\n                        graphite.events.trigger(\"ccSubscriptionInfo\", data);\n\n                    });\n                }\n            });\n        }\n        \n        var eulaAccepted = prefs.get(BracketsPreferencesModel.EULA_ACCEPTED);\n        \n        if (eulaAccepted) {\n            _logHelper();\n        } else {\n            graphite.events.on(\"eulaAccepted\", _logHelper);\n        }\n    }\n\n    function healthDataLoggingExistsInVersion() {\n        var currentBracketsVersion = brackets.metadata.version;\n        \n        var currentBracketsVersionValues    = currentBracketsVersion.split(/\\.|-/),\n            healthDataBracketsVersionValues = HEALTH_DATA_LOGGING_VERSION.split(/\\.|-/),\n            length                          = Math.max(currentBracketsVersionValues.length, healthDataBracketsVersionValues.length),\n            i;\n        \n        for (i = 0; i < length; i++) {\n            if (parseInt(currentBracketsVersionValues[i], 10) > parseInt(healthDataBracketsVersionValues[i], 10)) {\n                return true;\n            } else if (parseInt(currentBracketsVersionValues[i], 10) < parseInt(healthDataBracketsVersionValues[i], 10)) {\n                return false;\n            }\n        }\n        return true;\n    }\n    \n    if (healthDataLoggingExistsInVersion()) {\n        HealthLogger = brackets.getModule(\"utils/HealthLogger\");\n        AppInit.appReady(logUserInfo);\n    }\n    \n    exports.getAccessToken = getAccessToken;\n    exports.signOut        = signOut;\n    exports.isUserSignedIn = isUserSignedIn;\n    exports.isThorLoggedOut = isThorLoggedOut;\n});\n\n//# sourceURL=/./ims/IMSLib.js");

