eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2013-2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define: true, graphite: true, window: true, clearTimeout: true, setTimeout: true*/\n\ndefine(\'ccweb.files.extract/views/inspect/BaseInspectColorView\',[\n    \'jquery\',\n    \'underscore\',\n    \'backbone\',\n    \'ccweb.files.extract/controllers/SelectionController\',\n    \'ccweb.files.extract/controllers/DetailsController\',\n    \'ccweb.files.extract/models/ColorUsageModel\',\n    \'ccweb.files.extract/utils/StyleUtil\',\n    \'ccweb.files.extract/utils/TemplateUtil\',\n    \'text!ccweb.files.extract/views/templates/colorListItemTemplate.html\'\n], function ($, _, Backbone, SelectionController, DetailsController,\n        ColorUsageModel, StyleUtil, TemplateUtil, ColorListItemTemplate) {\n    \'use strict\';\n\n    var BaseInspectColorView = Backbone.View.extend({\n        events: {\n            \'click\': \'handleClick\'\n        },\n\n        initialize: function () {\n            this.render();\n            this.addHandlers();\n        },\n\n        render: function () {\n            this.setElement(TemplateUtil.createTemplate(ColorListItemTemplate));\n        },\n\n\n        addHandlers: function () {\n            DetailsController.on(\'change:selectedInspectItem\', this.handleSelectedInspectItemChange, this);\n            SelectionController.on(\'change:extractedStylesInSelection\', this.handleSelectionChange, this);\n        },\n\n        removeEventListeners: function () {\n            DetailsController.off(null, null, this);\n            SelectionController.off(null, null, this);\n        },\n\n        //------------------------------------------------\n        // Handlers\n        //------------------------------------------------\n\n        handleClick: function () {\n            if (this.model === DetailsController.get(\'selectedInspectItem\')) {\n                DetailsController.setSelectedInspectItem(null);\n            } else {\n                DetailsController.setSelectedInspectItem(this.model);\n\n                if (this.model instanceof ColorUsageModel) {\n                    this.showInfo();\n                }\n            }\n        },\n\n\n        handleSelectedInspectItemChange: function () {\n            if (this.model === DetailsController.get(\'selectedInspectItem\')) {\n                this.$el.addClass(\'selected\');\n            } else {\n                this.$el.removeClass(\'selected\');\n            }\n\n            if (!DetailsController.get(\'selectedInspectItem\')) {\n                graphite.events.trigger(\'hide-color-popup\', null);\n            }\n        },\n\n        handleSelectionChange: function () {\n            var stylesInSelection = this.getStylesForSelection(),\n                colorUsage = this.model,\n                found = false,\n                selectedLayers = SelectionController.getSelectedLayers();\n\n            $.each(stylesInSelection, function (index, value) {\n                if (StyleUtil.areUsageStylesEqual(colorUsage, value)) {\n                    found = true;\n                }\n            });\n\n            this.$el.removeClass(\'layer-over\');\n\n            if (found && selectedLayers.length > 0) {\n                this.$el.addClass(\'selected\');\n            } else {\n                this.$el.removeClass(\'selected\');\n            }\n        },\n\n        // override please\n        getStylesForSelection: function () {\n            // return an array of style models\n        },\n\n        // override please\n        getStylesForLayer: function (layer) {\n            // return an array of style usageModels.\n        },\n\n        showInfo: function () {\n            graphite.events.trigger(\'show-color-popup\', { style: this.model, sourceElement: this.$el.find(\'.color-chip\')});\n        },\n\n        remove: function () {\n            this.removeEventListeners();\n            return Backbone.View.prototype.remove.call(this);\n        }\n    });\n\n    return BaseInspectColorView;\n});\n\n//# sourceURL=/lib/graphite-api/public/js/ccweb.files.extract/views/inspect/BaseInspectColorView.js");

