eval("/*jslint nomen: true, devel: true*/\n/*global define: true, graphite: true, CSSListItemView: true*/\ndefine(\'ccweb.files.extract/views/inspect/CSSListItemView\',[\n    \'jquery\',\n    \'underscore\',\n    \'backbone\',\n    \'ccweb.files.extract/utils/TemplateUtil\',\n    \'ccweb.files.extract/utils/CSSUtil\'\n], function ($, _, Backbone, TemplateUtil, CSSUtil) {\n    \'use strict\';\n    var colorProperties = {\n        \'background-color\': 1,\n        \'border-color\': 1,\n        \'color\': 1\n    },\n        CSSListItemView = Backbone.View.extend({\n\n            defaults: {\n                cssObj : null,\n                clip : null\n            },\n\n            initialize: function (options) {\n                this.cssObj = options.cssObj;\n                this.preProcessor = options.preProcessor;\n                this.render();\n            },\n\n            render: function () {\n                var listItem = CSSUtil.cssPreprocessors[this.preProcessor].listItem(this.cssObj);\n                this.setElement(\'<li>\' + listItem + \'</li>\');\n\n                if (this.cssObj && this.cssObj.comment) {\n                    this.$el.append(\' <span class=\"comment\">/* \' + this.cssObj.comment + \' */</span>\');\n                }\n\n                return this;\n            },\n\n            isColorProperty: function () {\n                return this.cssObj && (colorProperties.hasOwnProperty(this.cssObj.property));\n            },\n\n            isSpacer: function () {\n                return this.cssObj && this.cssObj.spacer;\n            }\n        });\n\n    return CSSListItemView;\n});\n\n//# sourceURL=/lib/graphite-api/public/js/ccweb.files.extract/views/inspect/CSSListItemView.js");

