eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define: true, brackets: true*/\n\ndefine(\'ext/views/EditorCalloutView\',[\n    \'jquery\',\n    \'underscore\',\n    \'backbone\',\n    \'text!ext/views/editor-callout-view.html\',\n    \'ext/strings\'\n], function ($, _, Backbone, EditorCalloutViewTemplate, Strings) {\n    \"use strict\";\n\n    var AnimationUtils  = brackets.getModule(\"utils/AnimationUtils\"),\n        KeyEvent        = brackets.getModule(\"utils/KeyEvent\");\n\n    var EditorCalloutView = Backbone.View.extend({\n\n        codeMirror: null,\n        pos: null,\n        templateId: null,\n        message: \"\",\n        dismiss: null,\n        disableRemoveAnimation: false,\n\n        /**\n         * click .dismiss-button: trigger \"dismiss\" event\n         * click: remove callout from DOM\n         */\n        events: {\n            \"click .dismiss-button\": \"_handleDismiss\",\n            \"click\": \"remove\"\n        },\n\n        initialize: function (options) {\n            this.codeMirror = options.codeMirror;\n            this.pos = options.pos;\n            this.templateId = options.templateId;\n            this.message = options.message;\n            this.dismiss = options.dismiss || Strings.GOT_IT;\n            this.disableRemoveAnimation = options.disableRemoveAnimation || false;\n\n            this._onKeyDown = this._onKeyDown.bind(this);\n            this.codeMirror.on(\"keydown\", this._onKeyDown);\n\n            this.render();\n        },\n\n        _onKeyDown: function (cm, event) {\n            if (event.keyCode === KeyEvent.DOM_VK_ESCAPE) {\n                this.remove();\n            }\n        },\n\n        createTemplate: function () {\n            var srcHTML = $(EditorCalloutViewTemplate).filter(this.templateId).html(),\n                template = _.template(srcHTML);\n\n            return template(this).trim();\n        },\n\n        render: function () {\n            this.$el.html(this.createTemplate());\n            this.$el.addClass(\"editor-callout top\");\n\n            return this;\n        },\n\n        updatePosition: function () {\n            // Open the callout to get correct offsets and size\n            this.$el.addClass(\"open\");\n\n            var self = this,\n                calloutCoords = this.$el.offset(),\n                posCharBounds = this.codeMirror.charCoords(this.pos, \"local\"),\n                calloutBottom = calloutCoords.top + this.$el.outerHeight();\n\n            this.$el.removeClass(\"open\").addClass(\"visible\");\n\n            // Adjust vertical position if callout is\n            // (a) above viewport or \n            // (b) covers the editor position\n            if (calloutCoords.top < 0 || posCharBounds.bottom < calloutBottom) {\n                var notchHeight = this.$el.find(\".notch\").height();\n\n                this.$el.removeClass(\"top\").addClass(\"bottom\");\n                this.$el.css(\"top\", posCharBounds.bottom + notchHeight);\n            }\n            \n            AnimationUtils.animateUsingClass(this.el, \"animateOpen\").done(function () {\n                self.$el.addClass(\"open\");\n            });\n        },\n\n        /**\n         * @private\n         * Triggered when .dismiss-button is clicked. Note that callout is\n         * removed from DOM via click handler for $el.\n         */\n        _handleDismiss: function () {\n            this.trigger(\"dismiss\");\n        },\n\n        remove: function () {\n            var self = this,\n                doRemove = function () { Backbone.View.prototype.remove.call(self); };\n\n            this.codeMirror.off(\"keydown\", this._onKeyDown);\n\n            if (this.disableRemoveAnimation) {\n                doRemove();\n            } else {\n                AnimationUtils.animateUsingClass(self.el, \"animateClose\").always(doRemove);\n            }\n\n            this.codeMirror.focus();\n        }\n    });\n\n    return EditorCalloutView;\n\n});\n\n//# sourceURL=/./views/EditorCalloutView.js");

