eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, bitwise: true */\n/*global define, brackets, graphite, Mustache*/\n\ndefine(\'ext/models/ExtensionMetaData\',[\n    \"jquery\",\n    \"backbone\",\n    \"ext/models/BracketsPreferencesModel\",\n    \"ext/strings\",\n    \"text!ext/package.json\",\n    \"text!ext/html/eula-dialog.html\"\n], function ($, Backbone, BracketsPreferencesModel, Strings, PackageJSON, eulaDialogHTML) {\n    \"use strict\";\n    \n    var Dialogs = brackets.getModule(\"widgets/Dialogs\");\n    \n    var prefs = BracketsPreferencesModel.get(\"preferenceSystem\"),\n        eulaDialogTemplate = Mustache.render(eulaDialogHTML, Strings);\n\n    var ExtensionMetadata = Backbone.Model.extend({\n\n        initialize: function () {\n            var self = this,\n                id = JSON.parse(PackageJSON).name;\n\n            // Use compile-time metdata to get extension ID\n            this.set(\"id\", id);\n        },\n        \n        getAcceptedEULA: function () {\n            var deferred = new $.Deferred(),\n                eulaAccepted = prefs.get(BracketsPreferencesModel.EULA_ACCEPTED);\n            \n            if (eulaAccepted) {\n                deferred.resolve();\n            } else {\n                var dialogPromise = Dialogs.showModalDialogUsingTemplate(eulaDialogTemplate).getPromise();\n                dialogPromise.done(function (btnId) {\n                    var accepted = btnId === Dialogs.DIALOG_BTN_OK;\n                    prefs.set(BracketsPreferencesModel.EULA_ACCEPTED, accepted);\n                    \n                    if (accepted) {\n                        graphite.events.trigger(\"eulaAccepted\");\n                        deferred.resolve();\n                    } else {\n                        deferred.reject();\n                    }\n                });\n            }\n            \n            return deferred.promise();\n        },\n\n        getBundledBuildVersion: function () {\n            return prefs.get(BracketsPreferencesModel.INSTALL_BUNDLED_BUILD_VERSION);\n        },\n\n        isBundledBuild: function () {\n            return !!this.getBundledBuildVersion();\n        }\n    });\n\n    return new ExtensionMetadata();\n});\n\n//# sourceURL=/./models/ExtensionMetaData.js");

