eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2013-2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define, brackets, graphite, document*/\n\ndefine(\'override/views/detail/BracketsPSDDetailsView\',[\n    \'jquery\',\n    \'underscore\',\n    \'backbone\',\n    \'ccweb.files.extract/controllers/DetailsController\',\n    \'ccweb.files.extract/controllers/SelectionController\',\n    \'ccweb.files.extract/controllers/ZoomController\',\n    \'ccweb.files.extract/views/detail/PSDDetailsView\',\n    \'ccweb.files.extract/views/detail/SelectionBarView\',\n    \'ccweb.files.extract/views/preview/PSDPreviewView\',\n    \'ccweb.files.extract/views/popup/LayerThumbPopupView\',\n    \'ccweb.files.extract/views/detail/DetailPanelView\',\n    \'ccweb.files.extract/Constants\',\n    \'ext/Constants\',\n    \'ext/Strings\',\n    \'ccweb.files.extract/utils/TemplateUtil\'\n], function ($, _, Backbone, DetailsController, SelectionController,\n        ZoomController, PSDDetailsView, SelectionBarView, PSDPreviewView,\n        LayerThumbPopupView, DetailPanelView, Constants, PSDLensConstants,\n        Strings, TemplateUtil) {\n    \"use strict\";\n\n    var BracketsPSDDetailsView = PSDDetailsView.extend({\n\n        template: \'<div></div>\',\n\n        _renderSelectionBarView: function () {\n            // TODO monkey patch SelectionBarView instead?\n            // layers toggle button inside SelectionBarView\n            var $toggleLayers = $(\'<a href=\"#\" class=\"e4b-layers-toggle-button\" title=\"\' + Strings.SHOW_HIDE_LAYERS + \'\">&nbsp;</a>\');\n            $toggleLayers.addClass(\"selected\");\n            $toggleLayers.click(function () {\n                $toggleLayers.toggleClass(\"selected\");\n                $(\"#detail-panel\").toggle();\n\n                graphite.events.trigger(\"layers-panel-toggle\", {\n                    visible: $(\"#detail-panel:visible\").length > 0\n                });\n            });\n            \n            this.selectionBar.$el.prepend($toggleLayers);\n\n            // HACK E4B puts SelectionBarView into the main panel title bar\n            // where Parfait instead includes it within PSDDetailsView. The\n            // SelectionBarView lifecycle is still controlled by\n            // BracketsPSDDetailsView. We only change DOM insertion here.\n            var selectionBarEl = this.selectionBar.el,\n                $placeholder = $(\"#e4b-main-panel .e4b-selectionbar-placeholder\");\n\n            $placeholder.prepend(selectionBarEl);\n        },\n        \n        render: function () {\n            this.incompatibleBlendOptionsExist = false;\n\n            ZoomController.reset();\n            SelectionController.setPSDModel(this.model);\n            DetailsController.setPSDModel(this.model);\n            \n            this.detailPanelView = new DetailPanelView({model: this.model});\n            this.selectionBar = new SelectionBarView();\n            this.layerThumbPopup = new LayerThumbPopupView();\n            this.previewView = new PSDPreviewView({model: this.model});\n\n            this._renderSelectionBarView();\n            \n            // HACK Activate the Layers tab by default\n            graphite.events.on(\"drawPreviewFinish\", function () {\n                DetailsController.setSelectedTab(\"psdLayersTab\");\n            }, this);\n            \n            SelectionController.psdView = this.previewView;\n\n            this.$el.append(this.previewView.el);\n            this.$el.append(this.detailPanelView.el);\n            this.$el.append(this.layerThumbPopup.el);\n\n            return this;\n        },\n\n        handleModelImgdataChanged: function () {\n            // do nothing\n        },\n\n        handleRenderedSpritesChanged: function () {\n            // TODO where to show parfait notificaitons?\n            // if (this.incompatibleBlendOptionsExist) {\n            //     //add the notification message for incompatible blend mode\n            //     var messageObj = {};\n            //     messageObj.messageText = Strings.UNSUPPORTED_BLEND_MODES;\n            //     this.showNotification(messageObj);\n            // }\n        },\n\n        addHandlers: function () {\n            graphite.events.on(\"incompatibleGraphicOptionsInPSD\", function () { this.incompatibleBlendOptionsExist = true; }, this);\n        },\n\n        removeEventListeners: function () {\n            graphite.events.off(null, null, this);\n\n            // Overwrite parent behavior since KeyboardController is omitted here\n            // KeyboardController.removeKeyHandlers();\n        },\n\n        remove: function () {\n            SelectionController.changeSelection();\n            this.$el.removeClass(\"active\");\n            this.removeEventListeners();\n            var views = [\"detailPanelView\", \"selectionBar\", \"previewView\", \"layerThumbPopup\"];\n            this.destroyViews(views);\n            SelectionController.psdView = null;\n            this.currentNotification = null;\n            if (this.$notification) {\n                this.$notification.empty();\n            }\n            this.$el.empty();\n            this.stopListening();\n            return this;\n        }\n\n    });\n\n    return BracketsPSDDetailsView;\n\n});\n\n//# sourceURL=/./views/detail/BracketsPSDDetailsView.js");

