eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, maxerr: 50, regexp: true  */\n/*global define, brackets, graphite, Mustache, window, XMLHttpRequest, FileReader*/\n\ndefine(\'ext/ParfaitAPI\',[\n    \"exports\",\n    \"jquery\",\n    \"config/environment\",\n    \"controllers/AuthController\",\n    \"Constants\",\n    \"ext/NodeAPI\",\n    \"ext/ims/IMSLibWrapper\",\n    \'ext/ims/IMSLib\'\n], function (exports, $, environment, AuthController, Constants, NodeAPI, IMSLibWrapper, IMSLib) {\n    \"use strict\";\n\n    var FileSystem = brackets.getModule(\"filesystem/FileSystem\");\n\n    // TODO use serverAPI and remove these duplicate URLs\n    var AUTH_HEADER = \"Bearer {{accesstoken}}\",\n        nodeDomain = NodeAPI.nodeDomain,\n        configurePromise;\n\n    var RE_DATA_URL = /data:([^;\\s]+)?(;base64)?,(.*)/;\n\n    /**\n     * Map PSD GUIDs to map of layers to derived sprite img path\n     * @type\n     * {Object.<string, Object.<string, string>}\n     */\n    var derivedSpriteMap = {};\n    \n    function writeBase64File(file, data) {\n        return nodeDomain.exec(\"writeBase64\", file.fullPath, data);\n    }\n    \n    function writeDataURLFile(file, dataURL) {\n        // TODO sanity check dataURL\n        var exec = RE_DATA_URL.exec(dataURL);\n        return writeBase64File(file, exec[3]);\n    }\n\n    function _getAccessToken() {\n        return IMSLibWrapper.getAccessToken();\n    }\n    \n    function _wrapXHRPromise(promise) {\n        return promise.then(function (data) {\n            return data;\n        }, function (jqXHR, textStatus, errorThrown) {\n            return new $.Deferred().reject(errorThrown).promise();\n        });\n    }\n    \n    function updateAccessToken() {\n        return _getAccessToken().then(function (accessToken) {\n            // FIXME https://git.corp.adobe.com/jhagenst/brackets-parfait-extension/issues/628\n            // to remove this copy of the access token,\n            // change vanilla extract serverAPI calls to fetch access token asynch or use cookie.\n            AuthController.getAuthModel().attributes.access_token = accessToken;\n        });\n    }\n    \n    function _handleLoggedOut() {\n        // temp workaround to enable current server API\'s integration of access token\n        AuthController.getAuthModel().attributes.access_token = \"\";\n        graphite.events.trigger(\"logged-out\");\n    }\n\n    function configureCCProxies() {\n        if (!configurePromise) {\n            var hosts = [environment.FILESTORAGE_HOST, environment.IMAGE_SERVICE_HOST],\n                promises = [];\n            \n            hosts.forEach(function (host) {\n                var promise = nodeDomain.exec(\"configureProxy\", host, Constants.CACHE_LOCATION).then(function (res) {\n                    environment.setProxyURL(\"http://127.0.0.1:\" + res.port, host);\n                });\n                \n                promises.push(promise);\n            });\n            \n            configurePromise = $.when.apply(null, promises);\n        }\n\n        return configurePromise;\n    }\n    \n    function clearProxyCache() {\n        nodeDomain.exec(\"clearProxyCache\", Constants.CACHE_LOCATION);\n    }\n\n    function _handleProxyRequestComplete(event, url) {\n        graphite.events.trigger(\"proxy-request-complete\", { url: url });\n    }\n\n    function _handleProxyRequestError(event, url) {\n        graphite.events.trigger(\"proxy-request-error\", { url: url });\n    }\n\n    function _handleCacheFallbackComplete(event, url) {\n        graphite.events.trigger(\"cache-fallback-complete\", { url: url });\n    }\n\n    // Listen for authentication events\n    $(IMSLib).on(\"signInAuto\", updateAccessToken);\n    $(IMSLibWrapper).on(\"signIn\", updateAccessToken);\n    $(IMSLibWrapper).on(\"signOut\", _handleLoggedOut);\n    $(IMSLib).on(\"signOut\", _handleLoggedOut);\n\n    // Listen to proxy events\n    $(nodeDomain).on(\"proxyRequestComplete\", _handleProxyRequestComplete);\n    $(nodeDomain).on(\"proxyRequestError\", _handleProxyRequestError);\n    $(nodeDomain).on(\"cacheFallbackComplete\", _handleCacheFallbackComplete);\n\n    // Public API\n    exports.configureCCProxies      = configureCCProxies;\n    exports.clearProxyCache         = clearProxyCache;\n    exports.writeBase64File         = writeBase64File;\n    exports.writeDataURLFile        = writeDataURLFile;\n    exports.writeBase64File         = writeBase64File;\n    exports.updateAccessToken       = updateAccessToken;\n});\n\n//# sourceURL=/./ParfaitAPI.js");

