eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2013-2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define, graphite */\n\ndefine(\'override/views/preview/BracketsPSDPreviewView\',[\n    \'ccweb.files.extract/Constants\',\n    \'ccweb.files.extract/controllers/DetailsController\',\n    \'ccweb.files.extract/controllers/SelectionController\',\n    \'ccweb.files.extract/views/preview/PSDPreviewView\'\n], function (Constants, DetailsController, SelectionController, PSDPreviewView) {\n    \"use strict\";\n    \n    var BracketsPSDPreviewView = PSDPreviewView.extend({\n        \n        handleClickEvent: function (event) {\n            PSDPreviewView.prototype.handleClickEvent.call(this, event);\n            \n            // Patch to log metrics for layer selection\n            var activeTool = DetailsController.get(\"activeTool\");\n            if (activeTool === Constants.Tool.SELECT_DIRECT) {\n                var layerAtPoint = SelectionController.itemAtPoint(event.clientX, event.clientY);\n\n                if (layerAtPoint) {\n                    graphite.events.trigger(\"layer-select\", {\n                        source: \"preview\",\n                        numLayers: SelectionController.getSelectedLayers().length,\n                        type: layerAtPoint.get(\"type\")\n                    });\n                }\n            }\n        },\n\n        _loadPreviewImage: function () {\n            // Patch usage of img src with call to ImageServiceAPI/StorageServiceAPI\n            if (!this.model.get(\"localContent\")) {\n                var self = this,\n                    maxLen = Math.max(this.model.get(\"width\"), this.model.get(\"height\")),\n                    size = Math.round(maxLen / 2);\n\n                // size is used to define the longest edge of the rendition.\n                // We use half the size here for 2 reasons (1) smaller file\n                // download and (2) for the end user to see the progression\n                // from a somewhat pixelated JPG preview to the full-res PNG\n                // sprites.\n                graphite.serverAPI.getRendition(this.model, size, function (err, dataURI) {\n                    self.$tempPreview.attr(\"src\", dataURI);\n\n                    graphite.events.trigger(\"preview-rendition-finish\", {\n                        previewRenditionDuration: Date.now() - self.model.get(\"previewStarted\")\n                    });\n                });\n            } else {\n                PSDPreviewView.prototype._loadPreviewImage.call(this);\n            }\n        }\n        \n    });\n\n    return BracketsPSDPreviewView;\n});\n\n//# sourceURL=/./views/preview/BracketsPSDPreviewView.js");

