eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, regexp: true, sub: true, evil: true */\n/*global define, brackets, window, Mustache*/\ndefine(\'adobeid\',[\n    \"jquery\",\n    \"config/environment\",\n    \"ext/ims/IMSProxy\",\n    \"ext/models/ExtensionMetadata\",\n    \"ext/strings\",\n    \"ext/AdobeIMSStrings\",\n    \"text!html/ims-loading.html\"\n], function ($, environment, IMSProxy, ExtensionMetadata, Strings,\n    AdobeIMSStrings, IMSLoadingHTML) {\n    \"use strict\";\n    \n    var NativeApp = brackets.getModule(\"utils/NativeApp\");\n\n    var _imsWindow;\n\n    function _installPopupWindowHandlers(w) {\n        var windowHref = w.location.href;\n        windowHref = windowHref.slice(0, windowHref.lastIndexOf(\"/\") + 1);\n        // Change \"newwindow\" or target=_blank links to open in the OS default browser\n        // Copied from click-prevention handling brackets.js \n        w.document.body.addEventListener(\"click\", function (e) {\n            // Check parents too, in case link has inline formatting tags\n            var node = e.target, url, target, isNewWindow;\n            while (node) {\n                if (node.tagName === \"A\") {\n                    url = node.getAttribute(\"href\");\n                    target = node.getAttribute(\"target\");\n                    isNewWindow = (url.indexOf(\"newwindow\") === 0) || (target === \"_blank\");\n                    if (isNewWindow && !url.match(/^#/)) {\n                        NativeApp.openURLInDefaultBrowser(windowHref + url);\n                        e.preventDefault();\n                    }\n                    break;\n                }\n                node = node.parentElement;\n            }\n        }, true);\n    }\n    \n    // Create a module instead of setting a global window.adobeid\n    var adobeid = {},\n        $adobeid = $(adobeid),\n        platform = brackets.platform;\n\n    // Adobe Analytics accepts win, osx and linux names for `pl` property\n    if (platform === \"mac\") {\n        platform = \"osx\";\n    }\n    \n    adobeid.client_id = environment.IMS_CLIENT_ID;\n    adobeid.uses_modal_mode = true;\n    adobeid.locale = brackets.getLocale();\n    adobeid.target_env = environment.IMS_TARGET_ENV;\n    adobeid.redirect_uri = \"brackets-auth://redirectims.html\";\n    adobeid.override_modal_redirect = true;\n    adobeid.scope = \"AdobeID,openid,creative_cloud,creative_sdk\";\n    adobeid.strings = { user: AdobeIMSStrings };\n\n    adobeid.api_parameters = {\n        authorize: {\n            // SUSI Context https://selfservice.adobelogin.com\n            // ctx_id: \"signin\",\n            \n            // Adobe Analytics SUSI Integration\n            state: {\n                \"ac\": \"ExtractForBrackets_app\",\n                \"av\": ExtensionMetadata.get(\"version\"),\n                \"pl\": platform\n            }\n        }\n    };\n\n    adobeid.proxyGet = function (url, fnToCall, type) {\n        // Swap IMS host with proxy host:port\n        var proxyUrlInfo = IMSProxy.proxyUrlInfo(url);\n\n        return $.ajax({\n            url: proxyUrlInfo.url,\n            success: fnToCall,\n            dataType: type,\n            data: {\n                // FIXME Why doesn\'t $.ajax work with header assignment?\n                // Pass target in querystring\n                \"e4b-target-domain\": proxyUrlInfo.domain\n            }\n        });\n    };\n    adobeid.onReady = function () {\n        $adobeid.triggerHandler(\"ready\");\n    };\n    adobeid.onProfile = function (profile) {\n        $adobeid.triggerHandler(\"profile\", [profile]);\n    };\n    adobeid.onAccessToken = function (token, tokenInfo) {\n        $adobeid.triggerHandler(\"accessToken\", [token, tokenInfo]);\n    };\n    adobeid.onAccessTokenHasExpired = function (token, tokenInfo) {\n        $adobeid.triggerHandler(\"accessTokenHasExpired\", [token, tokenInfo]);\n    };\n    adobeid.onError = function (error) {\n        console.error(error.userMessage || error.message);\n        $adobeid.triggerHandler(\"error\", [error]);\n    };\n    adobeid.getClientWindow = function (url, params, fnOnRedirect) {\n        var deferred = new $.Deferred(),\n            interval,\n            exitOnRedirectInterval,\n            prevURL,\n            curURL,\n            documentElementHTML;\n\n        if (_imsWindow) {\n            _imsWindow.close();\n        }\n\n        try {\n            // Account for brackets-shell versions <= 0.44 that precisely test\n            // for \"about:blank\". Pass width/height URL params for\n            // brackets-shell to workaround bug\n            // https://github.com/adobe/brackets/issues/8768\n            _imsWindow = window.open(\"about:blank?width=\" + params.width + \"&height=\" + params.height);\n        } catch (err) {\n            _imsWindow = window.open(\"about:blank\");\n        }\n\n        // Use endpoint to choose label\n        var urlExec = /\\/ims\\/([^\\/]+)(\\/v[0-9]+)?(\\/([^\\/]+))?/i.exec(url),\n            endpoint = urlExec[4] ? urlExec[1] + \"_\" + urlExec[4] : urlExec[1],\n            labelKey = \"SUSI_LOADING_\" + endpoint.toUpperCase(),\n            label = Strings[labelKey] || Strings[\"SUSI_LOADING_BASE\"],\n            templateStrings = {};\n\n        templateStrings.LABEL = label;\n        templateStrings.WINDOW_TITLE = Strings[\"SUSI_LOADING_WINDOW_TITLE\"];\n\n        // Render loading screen\n        _imsWindow.document.write(Mustache.render(IMSLoadingHTML, templateStrings));\n\n        // Load IMS endpoint\n        _imsWindow.location.href = url;\n\n        // Use a Promise to allow listeners to respond when the popup window closes\n        $adobeid.triggerHandler(\"getClientWindow\", [deferred.promise()]);\n\n        // Remove interval when window closes\n        deferred.always(function () {\n            window.clearInterval(exitOnRedirectInterval);\n        });\n\n        function _stopPolling(isClosed) {\n            window.clearInterval(interval);\n\n            if (isClosed) {\n                // Resolve getClientWindow promise when we hit the redirect_uri\n                // Clears the exitOnRedirectInterval timer\n                deferred.resolve();\n            }\n        }\n\n        function _exitIfRedirect() {\n            if (_imsWindow) {\n                curURL = _imsWindow.location && _imsWindow.location.href;\n                if (curURL) {\n                    if (curURL.indexOf(adobeid.redirect_uri) === 0) {\n                    // Complete sign-in if we detect the redirect_uri\n                        fnOnRedirect(curURL);\n\n                        // Close pop-up window\n                        _imsWindow.close();\n                        _imsWindow = null;\n\n                        _stopPolling(true);\n\n                        return true;\n                    }\n                }\n            }\n            return false;\n        }\n\n        function _updateCookie() {\n            // Notify listeners of new/updated cookie\n            // Use eval() to suppress SecurityError that CEF reports, even when\n            // brackets-shell explicitly disables web security\n            $adobeid.triggerHandler(\"cookie\", [_imsWindow[\"eval\"](\"document.cookie\")]);\n        }\n\n        // FIXME Why is curURL \"data:text/html,chromewebdata\" instead\n        // of the redirect uri \"brackets-auth://...\", but only when\n        // we eval the \"cookie\" event at the start of the _onunload handler?\n        // Splitting up the work in _updateCookie fixes the bug somehow.\n        _imsWindow.addEventListener(\"unload\", _updateCookie);\n\n        // FIXME https://git.corp.adobe.com/jhagenst/brackets-parfait-extension/issues/782\n        // Somehow unload/load doesn\'t always catch the redirect URI on signUp only.\n        // Add persistent polling here as a HACK\n        exitOnRedirectInterval = window.setInterval(_exitIfRedirect, 500);\n\n        _imsWindow.addEventListener(\"unload\", function _onunload() {\n            // poll for URL change\n            interval = window.setInterval(function () {\n                if (_imsWindow) {\n                    if (_imsWindow.closed) {\n                        _stopPolling(true);\n                        return;\n                    }\n\n                    // Get HTML content of popup window\n                    if (_imsWindow.document && _imsWindow.document.documentElement) {\n                        documentElementHTML = _imsWindow.document.documentElement.innerHTML;\n                    } else {\n                        documentElementHTML = \"\";\n                    }\n\n                    if (_exitIfRedirect()) {\n                        return;\n                    } else if (documentElementHTML.indexOf(\"brackets.app.showDeveloperTools\") >= 0) {\n                        // Fire networkError error event back to IMSLibWrapper\n                        $adobeid.triggerHandler(\"error\", [{ name: \"networkError\" }]);\n\n                        // CEF OnLoadError page\n                        _imsWindow.close();\n\n                        _stopPolling(true);\n                    } else if (prevURL !== curURL) {\n                        _stopPolling(false);\n\n                        // Location changed (likely due to sign-up workflow)\n                        // Reset unload and cookie handling\n                        _imsWindow.addEventListener(\"unload\", _updateCookie);\n                        _imsWindow.addEventListener(\"unload\", _onunload);\n\n                        // Install click handler when page finishes loading\n                        _imsWindow.addEventListener(\"load\", function () {\n                            _installPopupWindowHandlers(_imsWindow);\n                        });\n\n                        prevURL = curURL;\n                    }\n                }\n            }, 1);\n        });\n\n        return _imsWindow;\n    };\n    \n    return adobeid;\n});\n\n//# sourceURL=/ims/adobeid.js");

