eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2013-2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define: true, graphite: true*/\n\ndefine(\'ccweb.files.extract/views/detail/HelpView\',[\n    \'jquery\',\n    \'underscore\',\n    \'backbone\',\n    \'ccweb.files.extract/utils/TemplateUtil\',\n    \'text!ccweb.files.extract/views/templates/helpDialogTemplate.html\',\n    \'ccweb.files.extract/utils/KeyboardUtils\'\n], function ($, _, Backbone, TemplateUtil, HelpDialogTemplate, KeyboardUtils) {\n    \'use strict\';\n\n    var HelpView = Backbone.View.extend({\n        visible: false,\n\n        events: {\n            \'click\': \'handleHide\'\n        },\n\n        blurTargets: \'.psd-preview-view, #detail-panel, .selection-controlbar, .psd-header\',\n\n        initialize: function () {\n            _.bindAll(this, \'handleToggle\', \'handleHide\');\n            this.render();\n\n            graphite.events.on(\'toggle-help-dialog\', this.handleToggle, this);\n            graphite.events.on(\'hide-help-dialog\', this.handleHide, this);\n\n        },\n\n        render: function () {\n            this.setElement(TemplateUtil.createTemplate(HelpDialogTemplate, {\n                ctrl: KeyboardUtils.getMetaKeyName(\'ctrl\'),\n                alt: KeyboardUtils.getMetaKeyName(\'alt\'),\n                shift: KeyboardUtils.getMetaKeyName(\'shift\')\n            }));\n            return this;\n        },\n\n        removeEventListeners: function () {\n            graphite.events.off(null, null, this);\n        },\n\n        remove: function () {\n            this.removeEventListeners();\n            return Backbone.View.prototype.remove.call(this);\n        },\n\n        //------------------------------------------------\n        // Handlers\n        //------------------------------------------------\n        handleToggle: function () {\n            if (this.visible) {\n                this.handleHide();\n            } else {\n                this.handleShow();\n            }\n        },\n\n        handleHide: function () {\n            $(this.blurTargets).removeClass(\'blur-background\');\n\n            this.$el.hide();\n            this.visible = false;\n        },\n\n        handleShow: function () {\n            $(this.blurTargets).addClass(\'blur-background\');\n\n            this.$el.show();\n            this.visible = true;\n            // for metrics fire this event when the help dialog is shown\n            graphite.events.trigger(\'help-dialog\');\n        }\n    });\n\n    return HelpView;\n});\n\n//# sourceURL=/lib/graphite-api/public/js/ccweb.files.extract/views/detail/HelpView.js");

