eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, maxerr: 50 */\n/*global define, brackets, graphite, Mustache, window */\n\ndefine(\'ext/MainPanel\',[\n    \"jquery\",\n    \"underscore\",\n    \"backbone\",\n    \"ext/command/Commands\",\n    \"ext/Constants\",\n    \"ext/ParfaitAPI\",\n    \"ccweb.files.extract/controllers/SelectionController\",\n    \"ext/models/BracketsPreferencesModel\",\n    \"ext/CacheManager\",\n    \"ext/models/ExtensionMetaData\",\n    \"ext/ims/IMSLibWrapper\",\n    \"ext/ims/IMSLib\",\n    \"text!ext/html/main-panel.html\",\n    \"ext/views/MainToolbarView\",\n    \"ccweb.files.extract/Constants\",\n    \"ext/strings\"\n], function ($, _, Backbone, Commands, PSDLensConstants, ParfaitAPI,\n              SelectionController, BracketsPreferencesModel, CacheManager,\n              ExtensionMetaData, IMSLibWrapper, ImsLib, PanelTemplate, MainToolbarView,\n              Constants, Strings) {\n    \"use strict\";\n\n    var MainViewManager     = brackets.getModule(\"view/MainViewManager\"),\n        Menus               = brackets.getModule(\"command/Menus\"),\n        ProjectManager      = brackets.getModule(\"project/ProjectManager\"),\n        WorkspaceManager    = brackets.getModule(\"view/WorkspaceManager\"),\n        NativeApp           = brackets.getModule(\"utils/NativeApp\");\n\n    var panel,\n        cmdTogglePanel,\n        cmdHelpTutorial,\n        currentRoute,\n        prefs = BracketsPreferencesModel.get(\"preferenceSystem\");\n\n    var $mainPanel,\n        $mainPanelTitle,\n        $mainToolbar,\n        mainToolbarView;\n    \n    function _hideFilesView() {\n        if (!panel.isVisible()) {\n            return;\n        }\n\n        // Abandonment metric\n        graphite.events.trigger(\"hide-panel\");\n        \n        // Hide the panel\n        panel.hide();\n        \n        // call router to destroy views\n        Backbone.history.navigate(\"hide\", true);\n        \n        // Restore UI\n        cmdTogglePanel.setChecked(false);\n        MainViewManager.focusActivePane();\n        WorkspaceManager.recomputeLayout();\n        \n    }\n\n    function showFilesView(showFirstLaunchOverride) {\n        function _doShowPanel(showFirstLaunch) {\n            if (showFirstLaunch) {\n                Backbone.history.navigate(\"first_launch\", true);\n            } else {\n                // navigate to the default route, which is the \n                // AssetListView.\n                // Pass a unique value to the Backbone history\n                // to ensure subsequent routes to AssetView always\n                // execute. This circumnavigates Backbones built in \n                // optimization to not execute a route if it\'s\n                // idenitical with the previous route.\n                Backbone.history.navigate(\"files/\", true);\n            }\n            panel.show();\n            WorkspaceManager.recomputeLayout();\n            cmdTogglePanel.setChecked(true);\n        }\n\n        // check if this is first launch\n        var isAuthorized = graphite.authModel.isValid();\n\n        if (showFirstLaunchOverride || !isAuthorized) {\n            _doShowPanel(true);\n        } else {\n            ParfaitAPI.configureCCProxies().done(function () {\n                _doShowPanel(false);\n            });\n        }\n    }\n\n    function toggle() {\n        if (panel.isVisible()) {\n            _hideFilesView();\n        } else {\n            showFilesView();\n        }\n    }\n    \n    function navigateTo(route) {\n        Backbone.history.navigate(route, true);\n    }\n    \n    /**\n     * @private\n     * call to show either the `Back to Files` or the extension icon in the primary toolbar\n     *\n     * @param {Boolean} showBackBtn `true` to show the \"back\" button.  `false` to show the extension icon\n     * @param {string} route\n     */\n    function _showBackToFilesIcon(showBackBtn, route) {\n        var $psdlLogoBtn = $mainPanel.find(\"#cc-logo\"),\n            $browseFilesBtn = $mainPanel.find(\"#browse-files\");\n        \n        if (showBackBtn) {\n            $psdlLogoBtn.hide();\n            if (route) {\n                $browseFilesBtn[0].setAttribute(\"data-route\", route);\n            }\n            $browseFilesBtn.show();\n        } else {\n            $psdlLogoBtn.show();\n            $browseFilesBtn.hide();\n        }\n    }\n        \n    function _getAssetCCPath() {\n        // FIXME https://git.corp.adobe.com/jhagenst/brackets-parfait-extension/issues/627\n        var path = Backbone.history.fragment || \"files\",\n            unencoded;\n\n        path = path.replace(/\\/$/, \"\");\n        unencoded = decodeURI(path);\n        return unencoded.substring(unencoded.indexOf(\"files/\"), unencoded.length);\n    }\n\n    function _handleRouteChange(newRoute) {\n        var psdPath = _getAssetCCPath(),\n            showFirstRunUI = (newRoute === PSDLensConstants.Routes.FIRST_LAUNCH),\n            showBackToFiles = (newRoute === PSDLensConstants.Routes.ASSET || newRoute === PSDLensConstants.Routes.AUTHLESS_ASSET),\n            isSignedIn = IMSLibWrapper.isSignedInUser() || ImsLib.isUserSignedIn(),\n            title = showBackToFiles ? decodeURIComponent(psdPath.substring(psdPath.lastIndexOf(\"/\") + 1, psdPath.length)) : \"\";\n        \n        $mainPanelTitle.off();\n        $mainPanelTitle.css(\"cursor\", \"default\");\n        \n        if (showFirstRunUI) {\n            mainToolbarView.displayFirstRunSection(true);\n        } else {\n            mainToolbarView.displayFirstRunSection(false);\n        }\n\n        if (showBackToFiles) {\n            mainToolbarView.displayPSDDetailsToolbar();\n            // for asset view, delay showing icon until the preview is rendered\n            _showBackToFilesIcon(true, isSignedIn ? currentRoute : \"first_launch\");\n        } else {\n            mainToolbarView.displayAssetListToolbar();\n            _showBackToFilesIcon(false);\n        }\n\n        $mainPanelTitle.text(title);\n        if (title.match(Constants.RE_SHORT_URL)) {\n            $mainPanelTitle.css(\"cursor\", \"pointer\");\n            $mainPanelTitle.click(function () {\n                NativeApp.openURLInDefaultBrowser(title);\n            });\n        }\n\n        currentRoute = Backbone.history.fragment;\n    }\n\n    function _handleSignOut() {\n        // TODO disable code hints when logged out\n        _hideFilesView();\n        // on logout clear any cached proxy content\n        CacheManager.purgeCache();\n    }\n\n    function registerMainPanel() {\n        // View > Show Extract for Brackets\n        cmdTogglePanel = Commands.register(Strings.SHOW_EXTRACT_FOR_BRACKETS, Commands.TOGGLE_PANEL, function () {\n            ExtensionMetaData.getAcceptedEULA().done(toggle);\n        });\n\n        // Internal command to show files view\n        // FIXME Use ccweb string for now\n        var rootLabel = \"Creative Cloud Assets\",\n            showAssetsRootLabel = Strings[rootLabel] || rootLabel;\n        \n        Commands.register(showAssetsRootLabel, Commands.SHOW_ASSETS_ROOT, function () {\n            ExtensionMetaData.getAcceptedEULA().done(showFilesView);\n        });\n        \n        // add Help commands and menu items\n        Commands.register(Strings.MENU_HELP_ONLINE, Commands.HELP_ONLINE, function () {\n            NativeApp.openURLInDefaultBrowser(Strings.HELP_ONLINE_URL);\n        });\n        cmdHelpTutorial = Commands.register(Strings.MENU_HELP_TUTORIAL, Commands.HELP_TUTORIAL, function () {\n            ExtensionMetaData.getAcceptedEULA().done(function () {\n                showFilesView(true);\n\n                // Force tutorial open (could be hidden due to HIDE_FIRST_LAUNCH_PSD pref)\n                Commands.execute(Commands.GUIDED_TUTORIAL);\n            });\n        });\n\n        var menu = Menus.getMenu(Menus.AppMenuBar.HELP_MENU);\n        menu.addMenuDivider();\n        menu.addMenuItem(Commands.HELP_ONLINE);\n        menu.addMenuItem(Commands.HELP_TUTORIAL);\n        menu.addMenuItem(Commands.TOGGLE_AUTH);\n\n        var viewMenu = Menus.getMenu(Menus.AppMenuBar.VIEW_MENU);\n        if (viewMenu) {\n            viewMenu.addMenuItem(Commands.TOGGLE_PANEL, [{\n                // Note: Brackets will write error message to console\n                // about key \"Cmd-Alt-/\" already being registered on mac.\n                key: \"Ctrl-Alt-X\"\n            }, {\n                // Define key again explicitly for mac to prevent Brackets\n                // from converting Ctrl to Cmd.\n                key: \"Ctrl-Alt-X\",\n                platform: \"mac\"\n            }]);\n        }\n\n        graphite.events.on(\"route-change\", _handleRouteChange);\n        \n        // Add the HTML UI\n        var panelHtml = Mustache.render(PanelTemplate, Strings);\n\n        // AppInit.htmlReady() has already executed before extensions are loaded\n        // so, for now, we need to call this ourself\n        panel = WorkspaceManager.createBottomPanel(\"e4b.mainpanel\", $(panelHtml), 200);\n        panel.hide();\n\n        $mainPanel = $(\"#e4b-main-panel\");\n        \n        // Add the main toolbar view\n        mainToolbarView = new MainToolbarView();\n        $mainToolbar = $mainPanel.find(\"#toolbar-placeholder\");\n        $mainToolbar.replaceWith(mainToolbarView.$el);\n        // Listen for events from toolbar\n        mainToolbarView.$el.on(\"navigateTo\", function (event, url) {\n            navigateTo(url);\n        });\n\n        $mainPanelTitle = $mainPanel.find(\".toolbar .title\");\n        \n        $mainPanel.find(\"#browse-files\").click(function (event) {\n            var route = event.target.attributes.getNamedItem(\"data-route\").value;\n            navigateTo(route);\n        });\n        \n        // Don\'t let parfait steal focus from CodeMirror or otherwise break its state\n        $mainPanel.on(\"mouseup\", function (event) {\n            if ((document.activeElement === document.body) &&\n                    ($(event.target).parents(\".allow-focus\").length === 0)) {\n                MainViewManager.focusActivePane();\n            }\n        });\n        \n        // enable close handler\n        $mainPanel.find(\".e4b-main-panel-close\").click(_hideFilesView);\n        \n        // settings\n        $mainPanel.find(\".e4b-settings-icon\").click(function () {\n            Commands.execute(Commands.PREF_DIALOG);\n        });\n        \n        SelectionController.clearDOMSelection = function () {};  // see bug #127\n        \n        $(ProjectManager).on(\"beforeProjectClose\", _hideFilesView);\n\n        $(IMSLibWrapper).on(\"signOut\", function () {\n            _handleSignOut();\n        });\n\n        $(ImsLib).on(\"signOut\", function () {\n            _handleSignOut();\n        });\n    }\n\n    return {\n        registerMainPanel: registerMainPanel\n    };\n});\n//# sourceURL=/./MainPanel.js");

