eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, bitwise: true, sub: true */\n/*global define, window, brackets, AppMeasurement*/\ndefine(\'ext/metrics/AnalyticsWrapper\',[\n    \"underscore\",\n    \"backbone\",\n    \"config/environment\",\n    \"ext/models/BracketsPreferencesModel\",\n    \"ext/models/ExtensionMetadata\",\n    \"ext/metrics/BaseMetricsWrapper\",\n    \"lib/adobe-analytics/AppMeasurement\"\n], function (_, Backbone, Environment, BracketsPreferencesModel, ExtensionMetadata,\n    BaseMetricsWrapper, AdobeAnalytics) {\n    \"use strict\";\n\n    var APPTYPE = \"desktop\",\n        NAMESPACE = \"ext4b\",\n        DESCRIPTION = \"description\",\n        COUNT = \"count\",\n        EVENT = \"event\",\n        AUTH_STEP = \"Auth Step\",\n        ACTION_DESCRIPTION = [APPTYPE, NAMESPACE, DESCRIPTION].join(\".\"),\n        ACTION_COUNT = [APPTYPE, NAMESPACE, COUNT].join(\".\"),\n        ACTION_EVENT = [APPTYPE, NAMESPACE, EVENT].join(\".\"),\n        SUBEVENT_MAP;\n    \n    // Mapping of IMS legacy subevents to Analytics values for adb.event.eventInfo.eventAction\n    SUBEVENT_MAP = {\n        \"LOGIN_START\":          { \"eventName\": AUTH_STEP, eventAction: \"Login Start\" },\n        \"VALID_ACCESS_TOKEN\":   { \"eventName\": AUTH_STEP, eventAction: \"Valid Authentication Token\" },\n        \"EXPIRED_ACCESS_TOKEN\": { \"eventName\": AUTH_STEP, eventAction: \"Expired Authentication Token\" },\n        \"MISSING_ACCESS_TOKEN\": { \"eventName\": AUTH_STEP, eventAction: \"Missing Authentication Token\" },\n        \"LOGIN_ATTEMPT\":        { \"eventName\": AUTH_STEP, eventAction: \"Login Attempt\" }, /* IMS reporting only */\n        \"LOGIN_SUCCESS\":        { \"eventName\": AUTH_STEP, eventAction: \"Login Success\" },\n        \"LOGIN_CANCEL\":         { \"eventName\": AUTH_STEP, eventAction: \"Login Cancel\" },\n        \"LOGIN_FAILURE\":        { \"eventName\": AUTH_STEP, eventAction: \"Login Failure\" }, /* IMS reporting only */\n        \"LOGOUT_ATTEMPTED\":     { \"eventName\": AUTH_STEP, eventAction: \"Logout Attempted\" },\n        \"LOGOUT_SUCCESS\":       { \"eventName\": AUTH_STEP, eventAction: \"Logout Success\" },\n        \"LOGOUT_CANCEL\":        { \"eventName\": AUTH_STEP, eventAction: \"Logout Cancel\" }, /* IMS reporting only...or impossible? */\n        \"LOGOUT_FAILURE\":       { \"eventName\": AUTH_STEP, eventAction: \"Logout Failure\" } /* IMS reporting only */\n    };\n    \n    var s = new AppMeasurement();\n    \n    // Shim code from @bbythewa\n    // ---------------\n    s._plugin_trackAction = s.trackAction = function (action, contextData) {\n        var s = this,\n            contextDataKey = \'\',\n            contextDataBackup = {},\n            linkTrackVars = [];\n\n        // backup\n        for (contextDataKey in s.contextData) {\n            if (s.contextData.hasOwnProperty(contextDataKey)) {\n                contextDataBackup[contextDataKey] = s.contextData[contextDataKey];\n            }\n        }\n\n        // reset\n        s.contextData = {};\n\n        // actionName special context data\n        s.contextData[\'a.action\'] = action;\n        linkTrackVars.push(\'contextData.a.action\');\n\n        // set the new contextData\n        for (contextDataKey in contextData) {\n            if (contextData.hasOwnProperty(contextDataKey)) {\n                linkTrackVars.push(\'contextData.\' + contextDataKey);\n                s.contextData[contextDataKey] = contextData[contextDataKey];\n            }\n        }\n\n        // whitelist\n        s.linkTrackVars = linkTrackVars.join(\',\');\n\n        // send\n        s.tl(true, \'o\', action);\n\n        // restore\n        for (contextDataKey in contextDataBackup) {\n            if (contextDataBackup.hasOwnProperty(contextDataKey)) {\n                s.contextData[contextDataKey] = contextDataBackup[contextDataKey];\n            }\n        }\n    };\n\n    s._plugin_trackAction.version = s.trackAction.version = \'1.1\';\n    // ---------------\n\n    // Original s.Ha() implementation for reference:\n    /*\n    s.Ha = function() {\n        var b;\n        if (!s.trackOffline ||\n            s.offlineThrottleDelay <= 0) return 0;\n        b = s.u() - s.ra;  // s.u() has no side effect. s.ra is the timestamp of the last attempted request.\n        if (s.offlineThrottleDelay < b) return 0;\n        return s.offlineThrottleDelay - b\n    };\n    */\n\n    // HACK: Patch s.Ha() method that computes timeout used to determine when\n    // to send the next event (uses offlineThrottleDelay)\n    var prevAction;\n    s.Ha = function () {\n        var nextAction = (s.e && s.e.length && s.e[0]) || false,\n            doForceOffline = false;\n\n        // Check for a non-empty buffer\n        if (nextAction) {\n            // If the prevAction still on the buffer, go offline\n            if (nextAction === prevAction) {\n                doForceOffline = true;\n            }\n            \n            prevAction = nextAction;\n        } else {\n            // Buffer is empty, let it fill up again\n            doForceOffline = true;\n        }\n\n        if (doForceOffline) {\n            s.forceOffline();\n        }\n\n        // If the buffer is non-empty send immediately, otherwise ignored\n        return 0;\n    };\n\n    // HACK: Patch s.da() method that sets up async sending of events\n    var _da = s.da;\n    s.da = function (b) {\n        // Only allow immediate send requests\n        if (b !== 0) {\n            return;\n        }\n\n        _da.call(s, b);\n    };\n    \n    s.account = Environment.ADOBE_ANALYTICS_ACCOUNT;\n    s.ssl = true;\n\n    // Always start offline and save events to the localStorage buffer\n    s.trackOffline = true;\n    s.forceOffline();\n    \n    // Copy value from input to output, then delete from input\n    function _setParam(input, output, originalKey, newKey, value) {\n        output[newKey] = value || input[originalKey];\n        delete input[originalKey];\n    }\n\n    var AnalyticsWrapper = BaseMetricsWrapper.extend({\n        \n        _objectMapping: function (eventName, input) {\n            var output = {},\n                subevent = input[\"subevent\"],\n                eventNameActionPair = SUBEVENT_MAP[subevent],\n                date = new Date(input[\"timestamp\"]);\n            \n            // Map legacy properties to Adobe Analytics required names\n            _setParam(input, output, \"subevent\",                   ACTION_EVENT,                       eventName + \":\" + subevent);\n            _setParam(input, output, \"extractExtensionVersion\",    \"adb.page.pageInfo.productVersion\");\n\n            // Mapped subevents define an eventName and eventAction, fallback to subevent\n            if (eventNameActionPair) {\n                output[\"adb.event.eventInfo.eventName\"] = eventNameActionPair.eventName;\n                output[\"adb.event.eventInfo.eventAction\"] = eventNameActionPair.eventAction;\n            } else {\n                output[\"adb.event.eventInfo.eventName\"] = subevent;\n            }\n            \n            // Auth Status: Use persistent properties `isLoggedIn` (true|false) and `isOffline` (true|false|\"unknown\")\n            var isLoggedIn = input[\"isLoggedIn\"],\n                isPaidAccount = input[\"isPaidAccount\"],\n                imsStatus = input[\"isLoggedIn\"] ? \"Logged In : \" : \"Logged Out : \",\n                isOffline = input[\"isOffline\"] === true,\n                onlineStatus = isOffline ? \"Offline\" : \"Online\",\n                registrationStatus = \"Not Registered\";\n            \n            if (isLoggedIn) {\n                registrationStatus = isPaidAccount ? \"Adobe Account (Paid)\" : \"Adobe Account (Free)\";\n            }\n            \n            output[\"adb.user.profile.attributes.regStatus\"] = registrationStatus;\n            output[\"adb.user.profile.attributes.authStatus\"] = imsStatus + onlineStatus;\n            delete input[\"isLoggedIn\"];\n            delete input[\"isOffline\"];\n            delete input[\"isPaidAccount\"];\n\n            // Add Adobe Analytics-only properties\n            output[\"adb.page.pageInfo.productCategory\"] = APPTYPE;\n            output[\"adb.page.pageInfo.namespace\"] = NAMESPACE;\n            output[\"adb.page.pageInfo.language\"] = brackets.app.language;\n            output[\"adb.page.pageInfo.category.primaryCategory\"] = input[\"state\"];\n            output[\"adb.user.profile.attributes.clientId\"] = Environment.IMS_CLIENT_ID;\n            \n            // TODO states are only for states...?\n            output[\"adb.page.pageInfo.pageName\"] = input[\"state\"];\n            delete input[\"state\"];\n            \n            // TODO verify format, preview label\n            output[\"a.AppID\"] = ExtensionMetadata.get(\"title\") + \" \" + ExtensionMetadata.get(\"version\");\n            \n            output[\"a.DayOfWeek\"] = date.getDay() + 1;\n            output[\"a.HourOfDay\"] = date.getHours() + 1;\n            \n            return output;\n        },\n        \n        _handleLifecycle: function (eventName, params) {\n            var prefs = this.getPrefs(),\n                lifecycle = prefs.get(BracketsPreferencesModel.METRICS_ANALYTICS_LIFECYCLE),\n                subevent = params[\"subevent\"],\n                isInstall = subevent === \"INSTALL_COMPLETE\",\n                isUpdate = subevent === \"UPDATE_COMPLETE\",\n                isLaunch = subevent === \"LAUNCH\",\n                retval = {},\n                dateString = new Date(params.timestamp).toLocaleDateString(\"en-US\");\n\n            // Require firstLaunchDate\n            if (lifecycle.all[\"adb.user.profile.attributes.firstLaunchDate\"] === undefined) {\n                lifecycle.all[\"adb.user.profile.attributes.firstLaunchDate\"] = dateString;\n            }\n\n            // Require InstallDate\n            if (lifecycle.all[\"a.InstallDate\"] === undefined) {\n                lifecycle.all[\"a.InstallDate\"] = dateString;\n            }\n            \n            // Note: lifecycle.all is merged into retval for all states and all actions\n            if (isInstall) {\n                // Only send a.InstallEvent in context data for this event instance\n                retval[\"a.InstallEvent\"] = 1;\n            } else if (isUpdate) {\n                if (lifecycle.all[\"adb.user.profile.attributes.firstUpgradeDate\"] === undefined) {\n                    lifecycle.all[\"adb.user.profile.attributes.firstUpgradeDate\"] = dateString;\n                }\n                \n                lifecycle.all[\"adb.user.profile.attributes.lastUpgradeDate\"] = dateString;\n                \n                // Only send a.UpgradeEvent in context data for this event instance\n                retval[\"a.UpgradeEvent\"] = 1;\n            } else if (isLaunch) {\n                var launches = lifecycle.launch[\"a.Launches\"] + 1;\n                lifecycle.launch[\"a.Launches\"] = launches;\n                \n                // Only send a.LaunchEvent and a.Launches for this event instance\n                if (isLaunch) {\n                    retval[\"a.LaunchEvent\"] = 1;\n                    retval[\"a.Launches\"] = launches;\n                }\n            }\n            \n            // Save all persistent context data\n            prefs.set(BracketsPreferencesModel.METRICS_ANALYTICS_LIFECYCLE, lifecycle);\n            \n            // Merge event-specific retval with properties for all states/all actions\n            retval = _.extend(retval, lifecycle.all);\n            \n            return retval;\n        },\n        \n        _customContextData: function (eventName, params) {\n            // Add prefix desktop.ext4b.description.key\n            var retval = {},\n                actionName;\n\n            _.each(params, function (value, key) {\n                // Numbers: desktop.ext4b.count\n                // Strings: desktop.ext4b.description\n                actionName = (typeof value === \"number\") ? ACTION_COUNT : ACTION_DESCRIPTION;\n                retval[actionName + \".\" + key] = value;\n            });\n            \n            return retval;\n        },\n        \n        handleEulaAccepted: function (accepted) {\n            if (accepted) {\n                this.flush();\n            }\n        },\n\n        _getBufferSize: function () {\n            return (s.e && s.e.length) || 0;\n        },\n\n        flush: function () {\n            if (this.isEulaAccepted() && this._getBufferSize()) {\n                // Go online\n                s.forceOnline();\n\n                // Pop next event off the buffer\n                s.W();\n            }\n        },\n        \n        track: function (eventName, params) {\n            // If the buffer is full, flush the buffer before sending the next event\n            if (this._getBufferSize() >= s.offlineLimit) {\n                this.flush();\n            }\n\n            var subevent = params[\"subevent\"];\n            \n            params = _.clone(params);\n            \n            // Add properties for all actions\n            // Note final _objectMapping() deletes original keys that are\n            // mapped for analytics e.g. extractExtensionVersion is deleted\n            // and adb.page.pageInfo.productVersion is used instead\n            var lifecycle = this._handleLifecycle(eventName, params),\n                contextData = this._customContextData(eventName, params),\n                globalSdr = this._objectMapping(eventName, params);\n            \n            // Merge it all into aaParams\n            _.extend(contextData, globalSdr, lifecycle);\n            \n            s.trackAction(subevent, contextData);\n        }\n\n    });\n\n    var aaWrapper = new AnalyticsWrapper();\n\n    // Initialize offline buffer size\n    s.offlineLimit = aaWrapper.BUFFER_SIZE;\n\n    return aaWrapper;\n});\n//# sourceURL=/./metrics/AnalyticsWrapper.js");

