eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, maxerr: 50, regexp: true */\n/*global define, brackets, graphite*/\n\n\ndefine(\'ext/controllers/LinkedRenameController\',[\n    \"jquery\",\n    \"underscore\",\n    \"backbone\",\n    \"ext/ParfaitAPI\",\n    \"ext/Constants\",\n    \"ext/controllers/CSSHintController\",\n    \"ext/views/EditorCalloutView\",\n    \"ext/models/BracketsPreferencesModel\",\n    \"ext/utils/StatusBarUtil\",\n    \"ext/strings\"\n], function ($, _, Backbone, ParfaitAPI, Constants, CSSHintController, EditorCalloutView, BracketsPreferencesModel, StatusBarUtil, Strings) {\n    \"use strict\";\n\n    var MainViewManager     = brackets.getModule(\"view/MainViewManager\"),\n        CodeMirror          = brackets.getModule(\"thirdparty/CodeMirror2/lib/codemirror\"),\n        DefaultDialogs      = brackets.getModule(\"widgets/DefaultDialogs\"),\n        Dialogs             = brackets.getModule(\"widgets/Dialogs\"),\n        FileUtils           = brackets.getModule(\"file/FileUtils\"),\n        FileSystem          = brackets.getModule(\"filesystem/FileSystem\"),\n        FileSystemError     = brackets.getModule(\"filesystem/FileSystemError\"),\n        ProjectManager      = brackets.getModule(\"project/ProjectManager\"),\n        BracketsStrings     = brackets.getModule(\"strings\"),\n        StringUtils         = brackets.getModule(\"utils/StringUtils\");\n    \n    var RE_URL_QUOTED = /^[\'\"]?([^\'\"]+)[\'\"]?$/,\n        prefs = BracketsPreferencesModel.get(\"preferenceSystem\");\n\n    function _isExitHandlerPass(linkedMode, marker) {\n        return !marker || linkedMode.exitPosition.marker === marker;\n    }\n    \n    // TODO: submit Brackets pull request to surface this functionality to all Brackets extensions\n    /**\n     * @private\n     * A string containing all invalid characters for a specific platform.\n     * This will be used to construct a regular expression for checking invalid filenames.\n     * When a filename with one of these invalid characters are detected, then it is \n     * also used to substitute the place holder of the error message.\n     */\n    var _invalidChars;\n\n    /**\n     * @private\n     * RegEx to validate if a filename is not allowed even if the system allows it.\n     * This is done to prevent cross-platform issues.\n     */\n    var _illegalFilenamesRegEx = /^(\\.+|com[1-9]|lpt[1-9]|nul|con|prn|aux|)$|\\.+$|\\/\\//i;\n    \n    var LinkedRenameController = Backbone.Model.extend({\n\n        $statusIndicator: null,\n\n        /**\n         * @private\n         * Confirms that a given filename is both valid and overwritable.\n         *   If there are invalid characters in the filename, prompts user to rename.\n         *   If the file already exists, prompts user to rename or overwrite.\n         *\n         * @param {!Editor} editor   A non-null editor object for the active window.\n         * @param {String} relPath\n         * @return {jQuery.Deferred} Resolved when the filename is ok to (over)write.\n         *   Rejected true when we\'re allowing the user to type another filename.\n         *   Rejected false when we should just stop and not write anything.\n         */\n        _confirmValidAndWriteableFilename: function (editor, relPath) {\n            var deferred = new $.Deferred(),\n                dialog,\n                body,\n                buttons;\n\n            // Check for invalid characters in the filename\n            if ((relPath.search(new RegExp(\"[\" + _invalidChars + \"]+\")) !== -1) ||\n                    (relPath.match(_illegalFilenamesRegEx))) {\n                // Prompt the user to either rename or cancel\n                body = StringUtils.format(\n                    Strings.EXTRACT_INVALID_CHARS,\n                    StringUtils.breakableUrl(relPath),\n                    _invalidChars\n                );\n\n                buttons = [\n                    {\n                        // Cleanup linked mode\n                        className : Dialogs.DIALOG_BTN_CLASS_NORMAL,\n                        id        : Dialogs.DIALOG_BTN_DONTSAVE,\n                        text      : BracketsStrings.CANCEL\n                    },\n                    {\n                        // Re-enter linked mode\n                        className : Dialogs.DIALOG_BTN_CLASS_PRIMARY,\n                        id        : Dialogs.DIALOG_BTN_CANCEL,\n                        text      : BracketsStrings.CMD_FILE_RENAME\n                    }\n                ];\n\n                dialog = Dialogs.showModalDialog(DefaultDialogs.DIALOG_ID_ERROR,\n                    BracketsStrings.ERROR_SAVING_FILE_TITLE, body, buttons);\n\n                dialog.getPromise().done(function (id) {\n                    if (id === Dialogs.DIALOG_BTN_CANCEL) {\n                        deferred.reject(true);  // allow user to type another filename\n                    } else {\n                        deferred.reject(false); // cancel -- don\'t download\n                    }\n                });\n            } else {\n                // Check if the file already exists\n                var isServerRoot = (relPath.charAt(0) === \"/\"),\n                    parentPath = isServerRoot ? ProjectManager.getProjectRoot().fullPath : editor.document.file.parentPath,\n                    path = parentPath + (isServerRoot ? relPath.substr(1) : relPath);\n\n                FileSystem.resolve(path, function (err, entry) {\n                    if (err !== FileSystemError.NOT_FOUND) {\n                        // A file with that filename already exists.  Prompt the user to either overwrite, rename, or cancel\n\n                        // String change in Brackets Release 0.40\n                        // See https://git.corp.adobe.com/jhagenst/brackets-parfait-extension/issues/195\n                        body = StringUtils.format(\n                            Strings.EXTRACT_ALREADY_EXISTS,\n                            (entry.isFile) ? BracketsStrings.FILE : BracketsStrings.DIRECTORY,\n                            StringUtils.breakableUrl(relPath)\n                        );\n\n                        if (entry.isFile) {\n                            // same name as an existing file.  Rename, overwrite, or cancel.\n                            buttons = [\n                                {\n                                    // Cleanup linked mode\n                                    className : Dialogs.DIALOG_BTN_CLASS_LEFT,\n                                    id        : Dialogs.DIALOG_BTN_DONTSAVE,\n                                    text      : BracketsStrings.DONT_SAVE\n                                },\n                                {\n                                    // Re-enter linked mode and rename\n                                    className : Dialogs.DIALOG_BTN_CLASS_NORMAL,\n                                    id        : Dialogs.DIALOG_BTN_CANCEL,\n                                    text      : BracketsStrings.CMD_FILE_RENAME\n                                },\n                                {\n                                    // Overwrite file and cleanup\n                                    className : Dialogs.DIALOG_BTN_CLASS_PRIMARY,\n                                    id        : Dialogs.DIALOG_BTN_OK,\n                                    text      : BracketsStrings.SAVE_AND_OVERWRITE\n                                }\n                            ];\n                        } else {\n                            // same name as an existing folder.  Rename or cancel.\n                            buttons = [\n                                {\n                                    // Cleanup linked mode\n                                    className : Dialogs.DIALOG_BTN_CLASS_NORMAL,\n                                    id        : Dialogs.DIALOG_BTN_DONTSAVE,\n                                    text      : BracketsStrings.CANCEL\n                                },\n                                {\n                                    // Re-enter linked mode\n                                    className : Dialogs.DIALOG_BTN_CLASS_PRIMARY,\n                                    id        : Dialogs.DIALOG_BTN_CANCEL,\n                                    text      : BracketsStrings.CMD_FILE_RENAME\n                                }\n                            ];\n                        }\n\n                        dialog = Dialogs.showModalDialog(DefaultDialogs.DIALOG_ID_ERROR,\n                            BracketsStrings.SAVE_AS, body, buttons);\n\n                        dialog.getPromise().done(function (id) {\n                            if (id === Dialogs.DIALOG_BTN_CANCEL) {\n                                deferred.reject(true);  // allow user to type another filename\n                            } else if (id === Dialogs.DIALOG_BTN_OK) {\n                                deferred.resolve(entry); // overwrite existing file\n                            } else { // id === DIALOG_BTN_DONTSAVE\n                                deferred.reject(false); // cancel -- don\'t download\n                            }\n                        });\n                    } else {\n                        // The filename is both valid and not already existing, so go ahead and allow the write\n                        deferred.resolve();\n                    }\n                });\n            }\n\n            return deferred.promise();\n        },\n        \n        /**\n         * @private\n         * Write image data to file path.\n         *\n         * @param {string} relPath\n         * @param {string} dataURL base64 image data\n         * @param {?File} entry FileSystemEntry for existing file; `undefined` otherwise\n         *\n         * @return {jQuery.Deferred} Resolved when file writes successfully.\n         *   Rejected if FileSystem returns an error (e.g. FileSystemError.ALREADY_EXISTS)\n         */\n        _writeFile: function (editor, relPath, dataURL, entry) {\n            var isServerRoot = (relPath.charAt(0) === \"/\"),\n                parentPath = isServerRoot ? ProjectManager.getProjectRoot().fullPath : editor.document.file.parentPath,\n                path = parentPath + (isServerRoot ? relPath.substr(1) : relPath),\n                file = entry || FileSystem.getFileForPath(path),\n                deferred = new $.Deferred();\n\n            // Save new file, overwriting if necessary\n            ParfaitAPI.writeDataURLFile(file, dataURL).then(\n                function () {\n                    deferred.resolve(file, dataURL);\n                },\n                function (nodeDomainErr) {\n                    // TODO convert nodeDomainErr to a FileSystemError?\n                    // unable to write the given file, even if asked to overwrite.\n                    deferred.reject(FileSystemError.NOT_WRITABLE, file.isFile);\n                }\n            );\n\n            return deferred.promise();\n        },\n\n        /**\n         * @private\n         * Show error dialog when _writeFile fails\n         *\n         * @param {string} err\n         * @param {boolean} isFile\n         * @param {string} relPath\n         *\n         * @return {jQuery.Deferred} Resolved when neither a rename or write\n         *   should be attempted. Rejected if another rename attempt is required.\n         */\n        _showWriteFileErrorDialog: function (err, isFile, relPath) {\n            var deferred = new $.Deferred(),\n                dialog,\n                buttons,\n                body;\n\n            body = StringUtils.format(\n                Strings.EXTRACT_UNABLE_TO_WRITE,\n                (isFile) ? BracketsStrings.FILE : BracketsStrings.DIRECTORY,\n                StringUtils.breakableUrl(relPath),\n                FileUtils.getFileErrorString(err)\n            );\n\n            buttons = [\n                {\n                    // Cleanup linked mode\n                    className : Dialogs.DIALOG_BTN_CLASS_NORMAL,\n                    id        : Dialogs.DIALOG_BTN_DONTSAVE,\n                    text      : BracketsStrings.CANCEL\n                },\n                {\n                    // Re-enter linked mode\n                    className : Dialogs.DIALOG_BTN_CLASS_PRIMARY,\n                    id        : Dialogs.DIALOG_BTN_CANCEL,\n                    text      : BracketsStrings.CMD_FILE_RENAME\n                }\n            ];\n\n            dialog = Dialogs.showModalDialog(\n                DefaultDialogs.DIALOG_ID_ERROR,\n                BracketsStrings.ERROR_SAVING_FILE_TITLE,\n                body,\n                buttons\n            );\n\n            dialog.getPromise().done(function (id) {\n                if (id === Dialogs.DIALOG_BTN_CANCEL) {\n                    // Re-enter linked mode with the up-to-date model\n                    deferred.reject();\n                } else { // id === DIALOG_BTN_DONTSAVE\n                    // Cleanup and skip writing\n                    deferred.resolve();\n                }\n\n                MainViewManager.focusActivePane();\n            });\n\n            return deferred.promise();\n        },\n\n        _showCallout: function (cm, pos, messageStr, prefKey, disableRemoveAnimation) {\n            var callout = new EditorCalloutView({\n                codeMirror: cm,\n                templateId: \"#callout\",\n                message: messageStr,\n                pos: pos,\n                disableRemoveAnimation: !!disableRemoveAnimation\n            });\n        \n            // Click handler for beforeRenameCallout button\n            callout.once(\"dismiss\", function () {\n                if (prefKey) {\n                    prefs.set(prefKey, true);\n                }\n            });\n\n            cm.addWidget(pos, callout.el, null, \"above\");\n            callout.updatePosition();\n\n            return callout;\n        },\n\n        /**\n         * Enter Linked Mode UI to rename image hints\n         * \n         * @param {Editor} editor\n         * @param {string} pathText\n         * @param {function} toDataURL\n         * @param {from:CodeMirror.Pos, to:CodeMirror.Pos} pathRange\n         * @param {CodeMirror.Pos} exitPos\n         * @param {Object} keyMap\n         */\n        enterLinkedMode: function (editor, pathText, toDataURL, pathRange, exitPos, keyMap) {\n            var self            = this,\n                cm              = editor._codeMirror,\n                exitLinkedPos   = new CodeMirror.LinkedPosition(cm.doc, exitPos),\n                linkedPos       = new CodeMirror.LinkedPosition(cm.doc, pathRange.from, pathRange.to),\n                linkedGroup     = new CodeMirror.LinkedPositionGroup([linkedPos]),\n                linkedModel     = new CodeMirror.LinkedModeModel([linkedGroup]),\n                exitHandler,\n                deferred        = new $.Deferred(),\n                beforeRenameCallout,\n                afterRenameCallout,\n                hideBeforeRenameMessage = prefs.get(BracketsPreferencesModel.HIDE_BEFORE_RENAME_MESSAGE),\n                hideAfterRenameMessage = prefs.get(BracketsPreferencesModel.HIDE_AFTER_RENAME_MESSAGE);\n\n            function _enterLinkedMode(text, options) {\n                options = options || {};\n                cm.enterLinkedMode(linkedModel);\n\n                // linked mode selects the full first group by default, select just the path text instead\n                var pathSepIndex    = text.lastIndexOf(\"/\"),\n                    fileExtIndex    = text.lastIndexOf(\".\"),\n                    startOffset     = options.startOffset || (pathSepIndex >= 0 ? pathSepIndex + 1 : 0),\n                    endOffset       = options.endOffset || (fileExtIndex >= 0 ? fileExtIndex : text.length),\n                    selStart        = new CodeMirror.Pos(linkedPos.from.line, linkedPos.from.ch + startOffset),\n                    selEnd          = new CodeMirror.Pos(linkedPos.to.line, linkedPos.from.ch + endOffset);\n\n                editor.setSelection(selStart, selEnd);\n\n                // Show first run message in a callout\n                if (!hideBeforeRenameMessage) {\n                    beforeRenameCallout = self._showCallout(\n                        cm,\n                        pathRange.from,\n                        Strings.EXTRACT_BEFORE_RENAME,\n                        BracketsPreferencesModel.HIDE_BEFORE_RENAME_MESSAGE,\n                        !hideAfterRenameMessage\n                    );\n                }\n            }\n\n            function cleanUp(file, dataURL) {\n                linkedGroup.off(\"exit\", exitHandler);\n\n                if (keyMap) {\n                    linkedModel.removeKeyMap(keyMap);\n                }\n                \n                if (file) {\n                    deferred.resolve(file, dataURL);\n                } else {\n                    deferred.reject();\n                }\n            }\n\n            function attemptWriteFile(relPath, dataURL, entry) {\n                return self._writeFile(editor, relPath, dataURL, entry).then(function (file, dataURL) {\n                    // Update callout to indicate completed download\n                    if (afterRenameCallout) {\n                        afterRenameCallout.$el.find(\".callout-download-status\").text(Strings.EXTRACT_DOWNLOAD_COMPLETE);\n                    }\n                  \n                    cleanUp(file, dataURL);\n                }, function (err, isFile) {\n                    if (afterRenameCallout) {\n                        afterRenameCallout.remove();\n                    }\n\n                    var errorPromise = self._showWriteFileErrorDialog(err, isFile, relPath);\n\n                    return errorPromise.then(function () {\n                        // DIALOG_BTN_DONTSAVE Don\'t Save button\n                        // DIALOG_BTN_DONTSAVE Cancel button (in case of unhandled error)\n                        // Exit linked mode and do not write file\n                        cleanUp();\n                    }, function () {\n                        // DIALOG_BTN_CANCEL Rename button\n                        // Re-enter linked mode with the up-to-date model\n                        // Use encodeURI in case it was re-written before async call\n                        // was fired.\n                        _enterLinkedMode(encodeURI(relPath));\n                    });\n                });\n            }\n\n            function requestAndWriteFile(relPath, fileExt) {\n                var decoded = decodeURI(relPath),\n                    encoded = encodeURI(decoded);\n\n                // Rewrite url(...) with URI encoded value\n                cm.replaceRange(encoded, linkedPos.from, linkedPos.to);\n\n                // Update LinkedPosition ranges manually\n                // TODO use text marker, see next comment\n                linkedPos.to.ch = linkedPos.from.ch + encoded.length;\n                exitLinkedPos.from.ch += (encoded.length - decoded.length);\n                \n                // check whether the filename is valid and/or whether the file already exists\n                self._confirmValidAndWriteableFilename(editor, relPath).done(function (entry) {\n                    // Request dataURL from service\n                    // FIXME this is a long running async call. The user may continue\n                    // editing the file during this window until the file write\n                    // completes. If an error occurs, we try to re-enter linked mode\n                    // with possibly invalid position data. Need to address this\n                    // race condition eventually. Blocking the UI isn\'t necessary if\n                    // we can maintain some temporary markers in the text and reset\n                    // the LinkedPosition(s) based on the markers.\n                    // FIXME do not make repeat request if file extension does not change\n                    var dataURLPromise = toDataURL(fileExt);\n\n                    // Do not show after-rename callout if promise was rejected synchronously\n                    if (dataURLPromise.state() !== \"rejected\") {\n                        // Show callout to explain workflow for rename/regenerate\n                        if (!hideAfterRenameMessage) {\n                            afterRenameCallout = self._showCallout(\n                                cm,\n                                pathRange.from,\n                                Strings.EXTRACT_AFTER_RENAME,\n                                BracketsPreferencesModel.HIDE_AFTER_RENAME_MESSAGE\n                            );\n\n                            // Dismiss this callout after any editor change\n                            // Add this event handler async to avoid the immediate change event\n                            // that happens via the replaceRange above\n                            window.setTimeout(function () {\n                                $(editor).one(\"change\", function () {\n                                    afterRenameCallout.remove();\n                                });\n                            }, 0);\n                        }\n\n                        // Update status bar indicator\n                        var statusText = StringUtils.format(Strings.EXTRACT_DOWNLOADING_STATUS, relPath);\n                        StatusBarUtil.showBusyStatus(statusText);\n                    }\n\n                    // Clear status bar indicator after download attempt\n                    dataURLPromise.fail(function () {\n                        if (afterRenameCallout) {\n                            afterRenameCallout.remove();\n                        }\n                    }).always(function () {\n                        StatusBarUtil.hideBusyStatus();\n                    });\n\n                    dataURLPromise.then(function (dataURL) {\n                        // dataURL request succeeded, attempt write to disk\n                        return attemptWriteFile(decoded, dataURL, entry);\n                    }, function (err, detail) {\n                        // dataURL request failed, rename or retry\n                        var buttons,\n                            doRetryRequest = false;\n\n                        if (err === Strings.SERVICE_ERROR) {\n                            // Show service failed dialog...Try again?\n                            // Buttons: OK and Cancel\n                            buttons = [\n                                {\n                                    // Cleanup linked mode\n                                    className : Dialogs.DIALOG_BTN_CLASS_NORMAL,\n                                    id        : Dialogs.DIALOG_BTN_CANCEL,\n                                    text      : BracketsStrings.CANCEL\n                                },\n                                {\n                                    // Retry download\n                                    className : Dialogs.DIALOG_BTN_CLASS_PRIMARY,\n                                    id        : Dialogs.DIALOG_BTN_OK,\n                                    text      : BracketsStrings.OK\n                                }\n                            ];\n\n                            doRetryRequest = true;\n                        } else if (err === Strings.STATIC_CONTENT_SERVICE_ERROR) {\n                            // Tried to extract unsupported layer(s) from a static PSD\n                            // Buttons: OK\n                            buttons = [\n                                {\n                                    // Exit linked mode\n                                    className : Dialogs.DIALOG_BTN_CLASS_PRIMARY,\n                                    id        : Dialogs.DIALOG_BTN_CANCEL,\n                                    text      : BracketsStrings.OK\n                                }\n                            ];\n                        } else {\n                            // SVG or unsupported file format for this layer\n                            // Buttons: Rename and Cancel\n                            buttons = [\n                                {\n                                    // Cleanup linked mode\n                                    className : Dialogs.DIALOG_BTN_CLASS_NORMAL,\n                                    id        : Dialogs.DIALOG_BTN_CANCEL,\n                                    text      : BracketsStrings.CANCEL\n                                },\n                                {\n                                    // Re-enter linked mode\n                                    className : Dialogs.DIALOG_BTN_CLASS_PRIMARY,\n                                    id        : Dialogs.DIALOG_BTN_OK,\n                                    text      : BracketsStrings.CMD_FILE_RENAME\n                                }\n                            ];\n                        }\n\n                        var retryDialog = Dialogs.showModalDialog(\n                            DefaultDialogs.DIALOG_ID_ERROR,\n                            BracketsStrings.ERROR_SAVING_FILE_TITLE,\n                            StringUtils.format(err, detail),\n                            buttons\n                        );\n\n                        retryDialog.getPromise().done(function (id) {\n                            if (id === Dialogs.DIALOG_BTN_OK) {\n                                if (doRetryRequest) {\n                                    // OK button, retry download\n                                    requestAndWriteFile(decoded, fileExt);\n                                } else {\n                                    // SVG Rename button, re-enter linked mode\n                                    // Set editor selection to file extension\n                                    var options = {\n                                        startOffset: encoded.lastIndexOf(\".\") + 1,\n                                        endOffset: encoded.length\n                                    };\n\n                                    _enterLinkedMode(encoded, options);\n                                }\n                            } else {\n                                // Exit linked mode\n                                cleanUp();\n                            }\n                        });\n                    });\n                }).fail(function (tryAgain) {\n                    if (tryAgain) {\n                        // not a valid filename, re-enter linked mode to try again\n                        _enterLinkedMode(relPath);\n                        MainViewManager.focusActivePane();\n                    } else {\n                        // Exit linked mode\n                        cleanUp();\n                    }\n                });\n            }\n\n            // linked mode \"exit\" event handler\n            exitHandler = function (group, doIt, res) {\n                if (beforeRenameCallout) {\n                    beforeRenameCallout.remove();\n                }\n                \n                // Do not attempt to save file if doIt is false (e.g. ESC key)\n                // or if the linked group has no text\n                if (!doIt || !res.text) {\n                    cleanUp();\n                    return;\n                }\n\n                // Strip leading/trailing quotes from url\n                var exec = Constants.RegExp.FILE_EXT.exec(res.text),\n                    fileExt = (exec && exec[1]) || \"png\",\n                    matches = res.text.match(RE_URL_QUOTED),\n                    relPath = (matches && matches[1]) || res.text,\n                    pathSepIndex = relPath.lastIndexOf(\"/\"),\n                    imagePath = pathSepIndex >= 0 ? relPath.substr(0, pathSepIndex + 1) : \"\";\n\n                // Save image path prefix\n                CSSHintController.setImagePath(imagePath);\n\n                requestAndWriteFile(relPath, fileExt);\n            };\n\n            linkedGroup.on(\"exit\", exitHandler);\n            linkedModel.setExit(exitLinkedPos);\n\n            _enterLinkedMode(pathText);\n\n            if (keyMap) {\n                // Wrap key map callbacks to CodeMirror.Pass when not at a\n                // marker or when at the exit marker\n                Object.keys(keyMap).forEach(function (key) {\n                    var mapFn = keyMap[key];\n\n                    if (typeof mapFn === \"function\") {\n                        keyMap[key] = function (cm, linkedMode, marker) {\n                            if (_isExitHandlerPass(linkedMode, marker)) {\n                                return CodeMirror.Pass;\n                            }\n\n                            return mapFn(cm, linkedMode, marker);\n                        };\n                    }\n                });\n\n                linkedModel.addKeyMap(keyMap);\n            }\n            \n            return deferred.promise();\n        }\n\n    });\n    \n    // TODO: submit Brackets pull request to surface this functionality to all Brackets extensions\n    // Init invalid characters string\n    if (brackets.platform === \"mac\") {\n        _invalidChars = \"?*|:\";\n    } else if (brackets.platform === \"linux\") {\n        _invalidChars = \"?*|\";\n    } else {\n        _invalidChars = \"?*:<>\\\\|\\\"\";\n    }\n\n    return new LinkedRenameController();\n});\n//# sourceURL=/./controllers/LinkedRenameController.js");

