eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, regexp: true */\n/*global define, brackets, graphite, Mustache*/\ndefine(\'ext/command/CommandHandlers\',[\n    \"jquery\",\n    \"underscore\",\n    \"backbone\",\n    \"ccweb.files.extract/main\", /*graphite global dependency*/\n    \"ccweb.files.extract/controllers/SelectionController\",\n    \"ext/Constants\",\n    \"ext/command/Commands\",\n    \"ext/controllers/CSSHintProvider\",\n    \"ext/controllers/HTMLHintProvider\",\n    \"ext/models/BracketsPreferencesModel\",\n    \"ext/views/CalloutView\",\n    \"text!ext/html/preferences-dialog.html\",\n    \"ext/strings\"\n], function ($, _, Backbone, ExtractPlugin, SelectionController, Constants,\n        Commands, CSSHintProvider, HTMLHintProvider, BracketsPreferencesModel,\n        CalloutView, preferencesDialogHTML, Strings) {\n    \"use strict\";\n    \n    var BracketsCommands    = brackets.getModule(\"command/Commands\"),\n        CodeHintManager     = brackets.getModule(\"editor/CodeHintManager\"),\n        CommandManager      = brackets.getModule(\"command/CommandManager\"),\n        Dialogs             = brackets.getModule(\"widgets/Dialogs\"),\n        EditorManager       = brackets.getModule(\"editor/EditorManager\"),\n        KeyBindingManager   = brackets.getModule(\"command/KeyBindingManager\"),\n        Menus               = brackets.getModule(\"command/Menus\");\n\n    var TUTORIAL_EVENT_HIDE = \"tutorial-hide\",\n        TUTORIAL_EVENT_SELECT_LAYER = \"tutorial-select-layer\",\n        TUTORIAL_EVENT_CODE_HINT = \"tutorial-code-hint\",\n        TUTORIAL_EVENT_MEASURE = \"tutorial-measurement\",\n        TUTORIAL_STEP_STATE_PENDING = \"pending\",\n        TUTORIAL_STEP_STATE_NEXT = \"next\",\n        TUTORIAL_STEP_STATE_COMPLETE = \"completed\",\n        TUTORIAL_STEP_STATE_REMOVED = \"removed\",\n        TUTORIAL_STEP_STATUS = \"tutorial-step-status\",\n        TUTORIAL_STEPS_CURRENT = \"tutorial-steps-current\",\n        TUTORIAL_STEPS_TOTAL = \"tutorial-steps-total\",\n        TUTORIAL_STEPS_COMPLETED = \"tutorial-steps-completed\",\n        TUTORIAL_NAVIGATED_RATE = \"tutorial-navigated-rate\",\n        TUTORIAL_COMPLETED_RATE = \"tutorial-completed-rate\";\n\n    var STEP_ORDER = [\n        TUTORIAL_EVENT_SELECT_LAYER,\n        TUTORIAL_EVENT_CODE_HINT,\n        TUTORIAL_EVENT_MEASURE\n    ];\n\n    var STEP_STATE_ORDER = [\n        TUTORIAL_STEP_STATE_PENDING,\n        TUTORIAL_STEP_STATE_REMOVED,\n        TUTORIAL_STEP_STATE_NEXT,\n        TUTORIAL_STEP_STATE_COMPLETE\n    ];\n    \n    var prefs = BracketsPreferencesModel.get(\"preferenceSystem\"),\n        cmdGuidedTutorial,\n        tutorialState = _.extend({}, Backbone.Events),\n        tutorialCompletion,\n        $body;\n    \n    function _showSettingsDialog() {\n        var newSettings = {},\n            template = Mustache.render(preferencesDialogHTML, {settings: BracketsPreferencesModel.toJSON(), \"Strings\": Strings}),\n            dialogStarted = Date.now(),\n            $template = $(template);\n        \n        $template.on(\"change\", \"[data-target]:checkbox\", function () {\n            var $target = $(this);\n            var attr = $target.attr(\"data-target\");\n            newSettings[attr] = $target.is(\":checked\");\n        });\n        \n        // TODO other input/select/radio prefs\n//        $template.on(\"input\", \"[data-target]:text\", function () {\n//            var $target = $(this);\n//            var attr = $target.attr(\"data-target\");\n//            newSettings[attr] = $target.val();\n//        });\n//        $template.on(\"change\", function () {\n//            var $target = $(\":selected\", this);\n//            var attr = $target.attr(\"data-target\");\n//\n//            if (attr) {\n//                newSettings.set(attr, $target.val());\n//            }\n//        });\n        \n        Dialogs.showModalDialogUsingTemplate($template).done(function (id) {\n            var doSave = id === \"save\",\n                metricsEnabledValue = newSettings[BracketsPreferencesModel.METRICS_ENABLED];\n            \n            // Special handling for metrics opt-out to trigger final queue flush\n            // and uninstall metrics event handlers. This is intentionally done\n            // before preferences-dialog-closed so that there\'s no question\n            // if we log new events after the checkbox in the dialog is unchecked.\n            if (doSave && (metricsEnabledValue === false)) {\n                prefs.set(BracketsPreferencesModel.METRICS_ENABLED, false);\n                delete newSettings[BracketsPreferencesModel.METRICS_ENABLED];\n            }\n            \n            graphite.events.trigger(\"preferences-dialog-closed\", {\n                duration: Date.now() - dialogStarted\n            });\n            \n            if (!doSave) {\n                return;\n            }\n            \n            // Go through each (non-opt-out) setting and apply it\n            Object.keys(newSettings).forEach(function (setting) {\n                var value = newSettings[setting];\n                \n                if (value !== undefined) {\n                    // Fire event first before saving to preference\n                    // This allows opt-out to be logged before changed\n                    graphite.events.trigger(\"preference-changed\", {\n                        preferenceKey: setting,\n                        preferenceValue: value\n                    });\n\n                    prefs.set(setting, value);\n                }\n            });\n        });\n        \n        graphite.events.trigger(\"preferences-dialog-opened\");\n    }\n\n    function _removeCurrentCallout() {\n        if (tutorialState.currentCallout) {\n            tutorialState.currentCallout.remove();\n            tutorialState.currentCallout = null;\n\n            return true;\n        }\n\n        return false;\n    }\n\n    function _updateCalloutPosition() {\n        if (tutorialState.currentCallout) {\n            tutorialState.currentCallout.updatePosition();\n        }\n    }\n\n    function _hideGuidedTutorial(reason) {\n        if (!_removeCurrentCallout()) {\n            return;\n        }\n\n        cmdGuidedTutorial.setChecked(false);\n\n        // Remove all callout event handlers\n        graphite.events.off(\"css-hint-show html-hint-show layer-select \" +\n            \"layerCompChangeComplete first-launch-call-to-action \" +\n            \"layers-panel-toggle \" +\n            \"main-toolbar-expanded main-toolbar-collapsed\", null, tutorialState);\n        window.removeEventListener(\"resize\", _updateCalloutPosition, true);\n\n        if ($body) {\n            $body.off(\"panelResizeUpdate\", _updateCalloutPosition);\n        }\n\n        // Log metrics\n        var completion = _.extend({ \"tutorial-hide-reason\": reason }, tutorialCompletion);\n        graphite.events.trigger(TUTORIAL_EVENT_HIDE, completion);\n    }\n\n    function _toggleGuidedTutorial() {\n        if (tutorialState.currentCallout) {\n            // Toggle off\n            tutorialState.trigger(TUTORIAL_EVENT_HIDE, \"toggle off\");\n        } else {\n            var deferred = new $.Deferred();\n\n            if (tutorialState.drawPreviewFinishPromise) {\n                tutorialState.drawPreviewFinishPromise.then(deferred.resolve);\n            } else {\n                deferred.resolve();\n            }\n\n            deferred.done(function () {\n                // Hardcoded to position the popup on the \"This is a PSD...\" in\n                // the getting started PSD\n                var $targetElement = $(\"#layer98\"),\n                    psdModel = SelectionController.getPSDModel(),\n                    isLocalContent = (psdModel && psdModel.get(\"localContent\")) || false;\n\n                // Check for the getting started PSD\n                if ($targetElement.length && isLocalContent) {\n                    // Scroll to the top of the getting started PSD\n                    $(\".psd-preview-view\")[0].scrollTop = 0;\n                } else {\n                    // Target the help button since we can\'t target a user\'s PSD\n                    $targetElement = $(\".e4b-help-button\");\n                }\n\n                // Toggle on\n                tutorialState.trigger(TUTORIAL_EVENT_SELECT_LAYER, $targetElement);\n                \n                // Reposition callout if window and/or panel size changes\n                window.addEventListener(\"resize\", _updateCalloutPosition, true);\n                $body.on(\"panelResizeUpdate\", _updateCalloutPosition);\n                graphite.events.on(\"layers-panel-toggle main-toolbar-expanded main-toolbar-collapsed\", _updateCalloutPosition, tutorialState);\n\n                cmdGuidedTutorial.setChecked(true);\n            });\n        }\n    }\n\n    function _updateLifetimeMetric(defaultParams, metricName, current) {\n        var previous = defaultParams[metricName],\n            max = Math.max(previous, current);\n\n        // Update tutorial pregression\n        defaultParams[metricName] = max;\n\n        return max;\n    }\n\n    function _logCalloutMetric(eventName, stepState) {\n        var defaultParams = prefs.get(BracketsPreferencesModel.METRICS_DEFAULT_PARAMS),\n            totalSteps = tutorialCompletion[TUTORIAL_STEPS_TOTAL],\n            navigatedSteps = tutorialCompletion[TUTORIAL_STEPS_CURRENT],\n            completedSteps = tutorialCompletion[TUTORIAL_STEPS_COMPLETED],\n            navigatedRate = navigatedSteps / totalSteps,\n            completedRate = completedSteps / totalSteps;\n\n        // Update rates for this tutorial session\n        tutorialCompletion[TUTORIAL_NAVIGATED_RATE] = navigatedRate;\n        tutorialCompletion[TUTORIAL_COMPLETED_RATE] = completedRate;\n\n        // Update max values\n        totalSteps = _updateLifetimeMetric(defaultParams, \"tutorialMax.totalSteps\", totalSteps);\n        navigatedSteps = _updateLifetimeMetric(defaultParams, \"tutorialMax.navigatedSteps\", navigatedSteps);\n        completedSteps = _updateLifetimeMetric(defaultParams, \"tutorialMax.completedSteps\", completedSteps);\n\n        // Update tutorial lifetime rates\n        _updateLifetimeMetric(defaultParams, \"tutorialMax.navigatedRate\", navigatedSteps / totalSteps);\n        _updateLifetimeMetric(defaultParams, \"tutorialMax.completedRate\", completedSteps / totalSteps);\n\n        // Update the furthest step reached\n        var maxNavigatedStep = STEP_ORDER[navigatedSteps - 1];\n        defaultParams[\"tutorialMax.lastNavigatedStep\"] = maxNavigatedStep;\n\n        // Update the furthest state reached for this event step\n        var maxParamName = \"tutorialMax.\" + eventName,\n            prevMaxState = defaultParams[maxParamName],\n            prevValue = prevMaxState ? STEP_STATE_ORDER.indexOf(prevMaxState) : 0,\n            currentValue = STEP_STATE_ORDER.indexOf(stepState);\n\n        if (currentValue >= prevValue) {\n            defaultParams[maxParamName] = stepState;\n        }\n\n        // Save prefs\n        prefs.set(BracketsPreferencesModel.METRICS_DEFAULT_PARAMS, defaultParams);\n\n        // Log metric\n        tutorialCompletion[TUTORIAL_STEP_STATUS] = tutorialCompletion[eventName] = stepState;\n        graphite.events.trigger(eventName, tutorialCompletion);\n    }\n    \n    /**\n     * @type {string} eventName\n     * @type {string} nextEventName\n     * @type {!jQueryObject|{left:number, right:number, top:number, bottom:number}} $targetElement\n     * @type {string} templateId\n     * @type {?string} dismissLabel\n     * @type {?string} $viewport\n     */\n    function _createCallout(eventName, nextEventName, $targetElement, templateId, dismissLabel, $viewport) {\n        _removeCurrentCallout();\n\n        var callout  = new CalloutView({\n            $targetElement: $targetElement,\n            $viewport: $viewport,\n            templateId: templateId,\n            disableRemoveAnimation: true,\n            dismissLabel: dismissLabel\n        });\n\n        var _goToNext = function () {\n            if (nextEventName) {\n                var reason = nextEventName === TUTORIAL_EVENT_HIDE ? \"done\" : undefined;\n                tutorialState.trigger(nextEventName, reason);\n            }\n        };\n\n        // Setup event handlers for metrics, i.e. next | complete | removed\n        callout.once(\"dismiss\", function () {\n            tutorialCompletion[eventName] = TUTORIAL_STEP_STATE_NEXT;\n            _goToNext();\n        });\n\n        callout.once(\"complete\", function () {\n            // When logging COMPLETE, we log whether the user completed all steps\n            tutorialCompletion[TUTORIAL_STEPS_COMPLETED]++;\n\n            tutorialCompletion[eventName] = TUTORIAL_STEP_STATE_COMPLETE;\n            _goToNext();\n        });\n\n        callout.once(\"remove\", function () {\n            callout.off(\"complete dismiss\");\n\n            // Update status when callout is removed\n            var stepState = tutorialCompletion[eventName];\n\n            if (stepState === TUTORIAL_STEP_STATE_PENDING) {\n                // Can only be PENDING if the user didn\'t hit NEXT/GOT_IT or didn\'t complete the task\n                stepState = TUTORIAL_STEP_STATE_REMOVED;\n            }\n\n            // Log metric when current callout is removed\n            _logCalloutMetric(eventName, stepState);\n        });\n\n        tutorialState.currentCallout = callout;\n\n        // Increment callout sequence number\n        tutorialCompletion[TUTORIAL_STEPS_CURRENT]++;\n\n        // Show extra page indicator when needed\n        if (tutorialCompletion[TUTORIAL_STEPS_TOTAL] >= 4) {\n            callout.$el.addClass(\"show-optional\");\n        }\n\n        // Log metric when showing current callout, status = PENDING\n        _logCalloutMetric(eventName, TUTORIAL_STEP_STATE_PENDING);\n\n        return callout;\n    }\n\n    function _completeCallout() {\n        tutorialState.currentCallout.trigger(\"complete\");\n    }\n\n    function _setupTutorial() {\n        var $targetElement;\n\n        // Remove current callout when the route changes\n        graphite.events.on(\"route-change\", function (route) {\n            tutorialState.trigger(TUTORIAL_EVENT_HIDE, \"route: \" + route);\n            tutorialState.isAssetListView = (route === Constants.Routes.FOLDER);\n            tutorialState.isFirstLaunch = (route === Constants.Routes.FIRST_LAUNCH);\n\n            if (tutorialState.isFirstLaunch) {\n                var deferred = new $.Deferred(),\n                    prefHideFirstLaunchUI = prefs.get(BracketsPreferencesModel.HIDE_FIRST_LAUNCH_PSD);\n\n                tutorialState.drawPreviewFinishPromise = deferred.promise();\n\n                // Wait for PSD to render, then resolve\n                graphite.events.once(\"drawPreviewFinish\", deferred.resolve);\n\n                // Show tutorial on first launch if the user hadn\'t already taken\n                // some call to action (open a psd button or creative url)\n                if (!prefHideFirstLaunchUI) {\n                    _toggleGuidedTutorial();\n                }\n            }\n\n            if (!$body) {\n                $body = $(window.document.body);\n            }\n        });\n\n        // Hide tutorial\n        tutorialState.on(TUTORIAL_EVENT_HIDE, _hideGuidedTutorial);\n\n        // 1 - Show \"select layer\" prompt\n        tutorialState.on(TUTORIAL_EVENT_SELECT_LAYER, function ($elem) {\n            $targetElement = $elem;\n\n            // Track completion\n            tutorialCompletion = {};\n            tutorialCompletion[TUTORIAL_STEPS_TOTAL] = tutorialState.isFirstLaunch ? 4 : 3;\n            tutorialCompletion[TUTORIAL_STEPS_COMPLETED] = 0;\n            tutorialCompletion[TUTORIAL_STEPS_CURRENT] = 0;\n\n            // Create and render the first callout (prompt to select a layer)\n            _createCallout(TUTORIAL_EVENT_SELECT_LAYER, TUTORIAL_EVENT_CODE_HINT, $targetElement, \"#guided-tutorial-1\", null, $(\".psd-preview-view\"));\n            \n            // Show the next callout when the user selects a layer\n            graphite.events.once(\"layer-select\", _completeCallout, tutorialState);\n\n            // Reposition callout if layer comp drop down appears\n            graphite.events.once(\"layerCompChangeComplete\", _updateCalloutPosition, tutorialState);\n        });\n\n        // 2 - Show \"code hint\" prompt\n        tutorialState.on(TUTORIAL_EVENT_CODE_HINT, function () {\n            // Target the active editor cursor, fallback to help button\n            var editor = EditorManager.getActiveEditor(),\n                target = $(\".e4b-help-button\");\n\n            if (editor) {\n                // Make sure cursor is in view\n                var cursorPos = editor.getCursorPos();\n                editor._codeMirror.scrollIntoView(cursorPos);\n                editor.focus();\n                \n                target = editor._codeMirror.charCoords(cursorPos);\n            }\n\n            // Prompt to show code hints\n            _createCallout(TUTORIAL_EVENT_CODE_HINT, TUTORIAL_EVENT_MEASURE, target, \"#guided-tutorial-2\");\n\n            graphite.events.once(\"css-hint-show html-hint-show\", _completeCallout, tutorialState);\n        });\n\n        // 3 - Show \"measurement\" prompt\n        tutorialState.on(TUTORIAL_EVENT_MEASURE, function () {\n            // Target the current selected layer\n            var selectedLayers = SelectionController.getSelectedLayers(),\n                nextEventName = TUTORIAL_EVENT_HIDE,\n                dismissLabel = Strings.GOT_IT;\n\n            if (selectedLayers.length > 0) {\n                var currentLayer = selectedLayers[0];\n                $targetElement = $(\"#layer\" + currentLayer.get(\"layerId\"));\n            }\n\n            // Prompt to show measurement popup\n            _createCallout(TUTORIAL_EVENT_MEASURE, nextEventName, $targetElement, \"#guided-tutorial-3\", dismissLabel, $(\".psd-preview-view\"));\n\n            // End tutorial when user sees measurement popup\n            graphite.events.on(\"layer-select\", function _multipleLayerSelect() {\n                if (SelectionController.getSelectedLayers().length > 1) {\n                    _completeCallout();\n                }\n            }, tutorialState);\n        });\n\n    }\n    \n    // (Hidden) Reset to first launch\n    Commands.register(Strings.RESET_TO_FIRST_LAUNCH, Commands.RESET_ALL, function () {\n        BracketsPreferencesModel.restoreDefaults();\n    });\n    KeyBindingManager.addBinding(Commands.RESET_ALL, \"Ctrl-Alt-C\");\n\n    // Guided Tutorial\n    cmdGuidedTutorial = Commands.register(Strings.CMD_GUIDED_TUTORIAL, Commands.GUIDED_TUTORIAL, function () {\n        // Navigate to /first_launch if currently on /files (AssetListView)\n        if (tutorialState.isAssetListView) {\n            // This will trigger a route-change, but not the tutorial due to\n            // HIDE_FIRST_LAUNCH_PSD being true (user is logged in)\n            Commands.execute(Commands.HELP_TUTORIAL);\n            return;\n        }\n\n        if (tutorialState.isFirstLaunch) {\n            // When in firstLaunch, running this command resets the tutorial instead of toggling\n            tutorialState.trigger(TUTORIAL_EVENT_HIDE, \"reset\");\n        }\n\n        _toggleGuidedTutorial();\n    });\n    _setupTutorial();\n    \n    // Preferences Dialog\n    Commands.register(Strings.PREF_DIALOG_TITLE, Commands.PREF_DIALOG, _showSettingsDialog);\n    \n    // Register code hints\n    CodeHintManager.registerHintProvider(new CSSHintProvider(), [\"css\", \"scss\", \"less\"], 10);\n    CodeHintManager.registerHintProvider(new HTMLHintProvider(), [\"html\"], 6); /* UrlCodeHints=5 */\n    \n    // Refresh code hints if open and PSD layer selection changes\n    // TODO determine the current provider is from PSD Lens\n    graphite.events.on(\"selection-changed\", function () {\n        if (CodeHintManager.isOpen()) {\n            CommandManager.execute(BracketsCommands.SHOW_CODE_HINTS);\n        }\n    });\n});\n\n//# sourceURL=/./command/CommandHandlers.js");

