eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2013 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define: true, graphite: true, brackets: true, PathUtils: true*/\n\ndefine(\'override/utils/TemplateUtil\',[\n    \"module\",\n    \"jquery\",\n    \"underscore\",\n    \"plugin-dependencies\"\n], function (module, $, _, deps) {\n    \"use strict\";\n\n    var ExtensionUtils = brackets.getModule(\"utils/ExtensionUtils\");\n\n    var modulePath = ExtensionUtils.getModulePath(module),\n        imagePath = \"file://\" + PathUtils.makePathAbsolute(\"../lib/graphite-api/public/images/\", modulePath);\n\n    var TemplateUtil = {\n\n        createTemplate: function (srcHTML, data) {\n            var normalizedHTML = srcHTML.replace(/(\\\"(\\/)?)images\\//g, \'\"\' + imagePath),\n                context = _.extend({translate: deps.translate}, data),\n                template = _.template(normalizedHTML);\n\n            return $(template(context).trim()).filter(\"*\");\n        }\n\n    };\n\n    return TemplateUtil;\n});\n\n//# sourceURL=/./utils/TemplateUtil.js");

