eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, maxerr: 50, regexp: true */\n/*global define, brackets, graphite*/\n\ndefine(\'ext/controllers/HTMLHintProvider\',[\n    \"jquery\",\n    \"ccweb.files.extract/Constants\",\n    \"ext/controllers/BaseHintProvider\",\n    \"ext/controllers/CSSHintController\",\n    \"ext/controllers/LinkedRenameController\",\n    \"ext/views/CSSHintView\",\n    \"ccweb.files.extract/controllers/SelectionController\"\n], function ($, ParfaitConstants, BaseHintProvider, CSSHintController, LinkedRenameController, CSSHintView, SelectionController) {\n    \"use strict\";\n    \n    var _                   = brackets.getModule(\"thirdparty/lodash\"),\n        CodeMirror          = brackets.getModule(\"thirdparty/CodeMirror2/lib/codemirror\"),\n        AppInit             = brackets.getModule(\"utils/AppInit\"),\n        ExtensionLoader     = brackets.getModule(\"utils/ExtensionLoader\"),\n        HTMLUtils           = brackets.getModule(\"language/HTMLUtils\"),\n        HTMLCodeHints;      // this is loaded through a special hack below, in AppReady handler\n    \n    var RE_SRC = /(src=[\\\"\\\']?)([^\\\"\\\']+\\.[^\\\"\\\']+)([\\\"\\\'].*)?$/,\n\n        TEXT_ATTR_MAP = {\n            \"alt\": true,\n            \"placeholder\": true,\n            \"title\": true,\n            \"value\": true\n        };\n    \n    var HTMLHintProvider = BaseHintProvider.extend({\n\n        initialize: function () {\n            this._implicitChar = null;\n        },\n        \n        _getTextLayers: function (layers) {\n            layers = layers || SelectionController.getSelectedLayers();\n            \n            // Text hints appear for element textContent and for specific attributes\n            return layers.filter(function (layerModel) {\n                if (layerModel.get(\"properties\").layerType === ParfaitConstants.Type.LAYER_TEXT) {\n                    return layerModel;\n                }\n            });\n        },\n        \n        _isTextHint: function (textLayers) {\n            var tokenType = this.tagInfo.position.tokenType,\n                attrName = this.tagInfo.attr && this.tagInfo.attr.name,\n                isTokenAttrValue = (tokenType === HTMLUtils.ATTR_VALUE),\n                isTextNode = false,\n                isTextAttribute = false;\n            \n            textLayers = textLayers || this._getTextLayers();\n            \n            if (textLayers.length) {\n                // tokenType is empty when cursor is inside textContent\n                isTextNode = !tokenType;\n\n                // Allow text hints as attribute values\n                isTextAttribute = isTokenAttrValue && TEXT_ATTR_MAP[attrName];\n            }\n            \n            return isTextNode || isTextAttribute;\n        },\n\n        _hasHints: function (editor, implicitChar) {\n            this.editor = editor;\n            \n            // TODO UrlHints\n            // Initialize hint-session state for plain HTML code hints, which we\'ll include in our getHints() results below\n            this._hasTagHints = HTMLCodeHints.tagHintProvider.hasHints(editor, implicitChar);\n            this._hasAttrHints = HTMLCodeHints.attrHintProvider.hasHints(editor, implicitChar);\n            \n            var hasSelectionHints = BaseHintProvider.prototype.hasHints.call(this, editor, implicitChar);\n            \n            if (!hasSelectionHints) {\n                return false;\n            }\n            \n            this.tagInfo = HTMLCodeHints.tagHintProvider.tagInfo;\n                                                   \n            var tokenType = this.tagInfo.position.tokenType,\n                attrName = this.tagInfo.attr && this.tagInfo.attr.name,\n                isTokenAttrValue = (tokenType === HTMLUtils.ATTR_VALUE),\n                isTextHint = this._isTextHint();\n            \n            if (isTextHint) {\n                return !implicitChar;\n            }\n            \n            // Image hints are provided in the following contexts:\n            // (1) Attribute name/value pair, e.g. [src=\"path/to/image.jpg\"] (TODO? [width=\"x\"] and [height=\"y\"])\n            // (2) img \"src\" attribute value only, e.g. [\"path/to/image.jpg\"]\n            if (this.tagInfo.tagName === \"img\") {\n                if (isTokenAttrValue) {\n                    // Validate (2), [path/to/image.jpg] attribute value hinting\n                    return (attrName === \"src\") || (attrName === \"width\") || (attrName === \"height\");\n                } else {\n                    // Validate (1), [src=\"...\"] attribute name and value hinting\n                    return !this.tagInfo.attr.name || \"src\".indexOf(this.tagInfo.attr.name) === 0;\n                }\n            }\n            \n            return false;\n        },\n        \n        /**\n         * Only returns true if this provider has unique hints (does not include merged hints)\n         *\n         * @param {Editor} editor \n         * A non-null editor object for the active window.\n         *\n         * @param {string} implicitChar \n         * Either null, if the hinting request was explicit, or a single character\n         * that represents the last insertion and that indicates an implicit\n         * hinting request.\n         *\n         * @return {boolean} \n         * Determines whether the current provider is able to provide hints for\n         * the given editor context and, in case implicitChar is non- null,\n         * whether it is appropriate to do so.\n         */\n        hasHints: function (editor, implicitChar) {\n            var result = this._hasHints(editor, implicitChar);\n\n            if (result) {\n                graphite.events.trigger(\"html-hint-show\", {\n                    context: this.tagInfo.position.tokenType || \"text\",\n                    implicitChar: implicitChar !== null\n                });\n            }\n\n            return result;\n        },\n\n        _getQuery: function () {\n            var query;\n\n            if (this.tagInfo.position.tokenType === HTMLUtils.ATTR_NAME) {\n                query = this.tagInfo.attr.name;\n            } else if (this.tagInfo.position.tokenType === HTMLUtils.ATTR_VALUE) {\n                query = this.tagInfo.attr.value;\n            } else if (this.tagInfo.tagName) {\n                query = this.tagInfo.tagName;\n            } else {\n                return null;\n            }\n\n            return query.substr(0, this.tagInfo.offset);\n        },\n        \n        /**\n         * Returns a list of availble HTML tag (full tag), attribute (full attr=value) or text content\n         * hints if possible for the current editor context. \n         * \n         * @param {string} implicitChar \n         * Either null, if the hinting request was explicit, or a single character\n         * that represents the last insertion and that indicates an implicit\n         * hinting request.\n         *\n         * @return {jQuery.Deferred|{\n         *              hints: Array.<string|jQueryObject>,\n         *              selectInitial: boolean}}\n         * Null if the provider wishes to end the hinting session. Otherwise, a\n         * response object that provides:\n         * 1. a sorted array hints that consists of strings\n         * 2. a boolean that indicates whether the first result, if one exists,\n         *    should be selected by default in the hint list window.\n         */\n        getHints: function (implicitChar) {\n            this._implicitChar = implicitChar;\n            this.tagInfo = (provider && provider.tagInfo) || HTMLUtils.getTagInfo(this.editor, this.editor.getCursorPos());\n\n            var self = this,\n                hints = [],\n                provider,\n                providerHints,\n                convertedProviderHints,\n                layers = SelectionController.getSelectedLayers(),\n                textLayers = this._getTextLayers(layers),\n                imgAttrNameAndValueHint = false,\n                imgSrcValueHint = false,\n                tokenType = this.tagInfo.position.tokenType,\n                className = \"parfait-hint-hide-description\",\n                context = \"property\";\n\n            // Determine hint context\n            var query = this._getQuery(),\n                isAttrValue = tokenType === HTMLUtils.ATTR_VALUE,\n                isInsideImgTag = this.tagInfo.tagName === \"img\",\n                isImgSrcAttr = isInsideImgTag && this.tagInfo.attr && (this.tagInfo.attr.name === \"src\"),\n                isImgAttrNameAndValue = isInsideImgTag && tokenType === HTMLUtils.ATTR_NAME;\n            \n            // Select delegate provider\n            if (this._hasTagHints) {\n                provider = HTMLCodeHints.tagHintProvider;\n            } else if (this._hasAttrHints || isAttrValue) {\n                provider = HTMLCodeHints.attrHintProvider;\n\n                if (isAttrValue) {\n                    // filter by value\n                    context = \"value\";\n\n                    // hide property name for attr value hints\n                    className += \" parfait-hint-hide-property\";\n                }\n            }\n            \n            // Fetch hints from delagate provider\n            providerHints = provider && provider.getHints(implicitChar);\n            \n            // Fetch text hints if standard attribute hints are not available\n            if (!providerHints && this._isTextHint(textLayers) && !implicitChar) {\n                hints = hints.concat(CSSHintController.getTextValues(textLayers, isAttrValue));\n            }\n            \n            // Convert plain hints to DOM, use results as query flags for image hints\n            if (providerHints && providerHints.hints) {\n                convertedProviderHints = providerHints.hints.map(function (plainHint) {\n                    var indentWrapper = $(\"<span class=\'parfait-plain-hint\'></span>\")\n                        .text(plainHint)\n                        .data({ provider: provider, plainHint: true });\n\n                    if (!imgAttrNameAndValueHint && isInsideImgTag && self._hasAttrHints) {\n                        // Flag to insert [src|width|hright=\"...\"] if provider includes it\n                        imgAttrNameAndValueHint = isImgAttrNameAndValue &&\n                            (plainHint === \"src\" || plainHint === \"width\" || plainHint === \"height\");\n                    }\n\n                    return indentWrapper;\n                });\n            }\n            \n            // Flag to insert [path/to/image.jpg] for attribute value hinting\n            imgSrcValueHint = isImgSrcAttr && isAttrValue && this.tagInfo.attr;\n            \n            // Add image hints\n            if (imgAttrNameAndValueHint || imgSrcValueHint || isAttrValue) {\n                var models = [];\n                \n                if (imgAttrNameAndValueHint) {\n                    var measurements = CSSHintController.getMeasurementValues(layers);\n                    models = CSSHintController.getImgSrcNameAndValues(layers);\n                    models = models.concat(measurements);\n                } else if (imgSrcValueHint) {\n                    models = CSSHintController.getImgSrcValues(layers);\n                } else if (isInsideImgTag && isAttrValue) {\n                    models = CSSHintController.getMeasurementValues(layers, {\n                        measurmentType: this.tagInfo.attr.name\n                    });\n                }\n\n                hints = hints.concat(models);\n            }\n            \n            // Filter and sort\n            if (hints.length) {\n                if (query) {\n                    hints = hints.filter(function (model) {\n                        return model.get(context).indexOf(query) >= 0;\n                    });\n                }\n    \n                hints = hints.sort(this.hintModelCompare);\n    \n                // Convert to code hint view\n                hints = _.map(hints, function (model) {\n                    model.set(\"providerType\", \"html\");\n\n                    var view = new CSSHintView({\n                        model: model,\n                        className: className\n                    });\n\n                    // Save HTMLCodeHints.tagHintProvider or HTMLCodeHints.attrHintProvider\n                    // to handle string insertion when hint is selected\n                    return view.$el.data(\"provider\", provider);\n                });\n            }\n            \n            // Merge with delegate providers\n            if (convertedProviderHints) {\n                hints = hints.concat(convertedProviderHints);\n            }\n            \n            return {\n                hints: hints,\n                selectInitial: true\n            };\n        },\n        \n        _insertStringHint: function (hint) {\n            this.editor.document.replaceRange(hint, this.editor.getCursorPos());\n        },\n\n        /**\n         * Inserts a given HTML tag or attribute value hint into the current editor context. \n         * \n         * @param {String|jQuery} hint \n         * The hint to be inserted into the editor context.\n         * \n         * @return {Boolean} \n         * Indicates whether the manager should follow hint insertion with an\n         * additional explicit hint request.\n         */\n        insertHint: function ($hint) {\n            var self = this,\n                provider = $hint.data(\"provider\"),\n                plainHint = $hint.data(\"plainHint\"),\n                result = false,\n                toHint = (plainHint) ? function () { return $hint.text(); } : $hint.data(\"toHint\"),\n                text = toHint(this.tagInfo.position.tokenType),\n                property = !plainHint && $hint.data(\"property\"),\n                hintType = !plainHint && $hint.data(\"type\"),\n                toDataURL = $hint.data(\"toDataURL\"),\n                beforePos = this.editor.getCursorPos();\n\n            if (provider) {\n                // Delegate hint insertion to provider\n                result = provider.insertHint(text);\n            } else {\n                // Basic text insertion (textContent and src value) at current cursor position\n                this._insertStringHint(text);\n            }\n\n            if (plainHint) {\n                return result;\n            }\n\n            graphite.events.trigger(\"html-hint-insert\", {\n                property: property,\n                context: this.tagInfo.position.tokenType,\n                hintType: hintType,\n                implicitChar: this._implicitChar !== null\n            });\n\n            // Enter linked mode when an image hint is selected\n            if (toDataURL) {\n                var line = this.editor.document.getLine(beforePos.line),\n                    indexOf = line.indexOf(text), /* FIXME edge case if the code hint text appears multiple times on this line */\n                    matches = line.match(RE_SRC),\n                    path = matches[2],\n                    leadingText = (matches && matches[1]) || \"\",\n                    fromCh = matches.index + leadingText.length,\n                    range = {\n                        from: new CodeMirror.Pos(beforePos.line, fromCh),\n                        to: new CodeMirror.Pos(beforePos.line, fromCh + path.length)\n                    },\n                    exitPos = this.editor.getCursorPos();\n\n                var keyMap = {\n                    name: \"exitKeyMap\",\n                    \"\'\\\"\'\": function (cm, linkedMode, marker) {\n                        // Exit normally\n                        linkedMode.exit(true, true);\n                    },\n                    \"\'>\'\": function (cm, linkedMode, marker) {\n                        // Handle \'>\' within the src attr value\n                        var markerFind = linkedMode.exitPosition.marker.find(),\n                            markerRange = (markerFind.from) ? markerFind : { from: markerFind, to: markerFind },\n                            afterPos = { line: markerRange.to.line, ch: markerRange.to.ch + 1 };\n\n                        // This operation triggers an exiting change event\n                        self.editor.document.replaceRange(\">\", afterPos, afterPos);\n\n                        // Move cursor inserted exit char\n                        self.editor.setCursorPos({ line: afterPos.line, ch: afterPos.ch + 1 });\n                    }\n                };\n\n                // Make sure the range passed to link mode is valid.\n                // When the range is not parsed correctly, i.e. not valid, we will see \n                // path === line and leadingText === \"\"\n                if (matches) {\n                    // TODO update exitPos if matching \'>\' is found\n                    // Note that exitPos is a new position after calling insertHint() on the delegate provider\n                    var renamePromise = LinkedRenameController.enterLinkedMode(self.editor, path, toDataURL, range, exitPos, keyMap);\n                    renamePromise.done(function () {\n                        // TODO how to reload an img tag?\n                        //self._updateLivePreview();\n                    });\n                }\n            }\n\n            return false;\n        }\n    });\n    \n    // Get a reference to the default Brackets built-in HTML code hints provider\n    AppInit.appReady(function () {\n        var r = ExtensionLoader.getRequireContextForExtension(\"HTMLCodeHints\");\n        HTMLCodeHints = r(\"main\");\n    });\n\n    return HTMLHintProvider;\n});\n\n//# sourceURL=/./controllers/HTMLHintProvider.js");

