eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2013-2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define: true, graphite: true*/\n\ndefine(\'ccweb.files.extract/views/inspect/InspectFontFaceView\',[\n    \'jquery\',\n    \'underscore\',\n    \'backbone\',\n    \'ccweb.files.extract/controllers/SelectionController\',\n    \'ccweb.files.extract/controllers/DetailsController\',\n    \'ccweb.files.extract/utils/StyleUtil\',\n    \'ccweb.files.extract/utils/TemplateUtil\',\n    \'ccweb.files.extract/utils/CSSUtil\',\n    \'text!ccweb.files.extract/views/templates/fontFaceListItemTemplate.html\'\n], function ($, _, Backbone, SelectionController, DetailsController, StyleUtil, TemplateUtil, CSSUtil,\n    FontFaceListItemTemplate) {\n    \'use strict\';\n\n    var InspectFontFaceView = Backbone.View.extend({\n        initialize: function (data) {\n            this.ppi = data.ppi;\n            this.render();\n            this.addHandlers();\n        },\n\n        render: function () {\n            var self = this,\n                context = {\n                    convert: function (x) {\n                        return CSSUtil.convertPSDUnitsToPreferredUnits(x, true);\n                    }\n                };\n\n            context = _.extend(context, this.model);\n\n            this.model.styles.sort(this.sortTextSizeFunction);\n            this.setElement(TemplateUtil.createTemplate(FontFaceListItemTemplate, context));\n\n            this.$el.find(\'.text-style-size\').each(function (index) {\n                $(this).data(\'model\', self.model.styles[index]);\n            });\n        },\n\n        addHandlers: function () {\n            var self = this;\n            this.$el.find(\'.text-style-size\').click(function () {\n                if ($(this).data(\'model\') === DetailsController.get(\'selectedInspectItem\')) {\n                    DetailsController.setSelectedInspectItem(null);\n                    graphite.events.trigger(\'hide-extract-code-popup\');\n                } else {\n                    DetailsController.setSelectedInspectItem($(this).data(\'model\'));\n                    graphite.events.trigger(\'show-extract-code-popup\',\n                        {sourceElement: $(this), model: $(this).data(\'model\'), ppi: self.ppi});\n\n                }\n            });\n\n            DetailsController.on(\'change:selectedInspectItem\', this.handleSelectedInspectItemChange, this);\n            SelectionController.on(\'change:extractedStylesInSelection\', this.handleSelectionChange, this);\n        },\n\n        removeEventListeners: function () {\n            DetailsController.off(null, null, this);\n            SelectionController.off(null, null, this);\n        },\n\n        sortTextSizeFunction: function (a, b) {\n            return parseFloat(a.get(\'style\').get(\'size\')) - parseFloat(b.get(\'style\').get(\'size\'));\n        },\n\n        //------------------------------------------------\n        // Handlers\n        //------------------------------------------------\n        handleSelectedInspectItemChange: function () {\n            var selectedInspectItem = DetailsController.get(\'selectedInspectItem\');\n            this.$el.find(\'.text-style-size\').each(function (index, value) {\n                if (StyleUtil.areUsageStylesEqual($(value).data(\'model\'), selectedInspectItem)) {\n                    $(value).addClass(\'selected\');\n                } else {\n                    $(value).removeClass(\'selected\');\n                }\n            });\n        },\n\n\n        handleSelectionChange: function () {\n            var textStylesInSelection =\n                SelectionController.get(\'extractedStylesInSelection\').textStyles;\n            var found = false;\n            var selectedLayers = SelectionController.getSelectedLayers();\n\n            this.$el.find(\'.text-style-size\').each(function (index, value) {\n                found = false;\n                $.each(textStylesInSelection, function (textStyleIndex, textStyle) {\n                    if (StyleUtil.areUsageStylesEqual(textStyle, $(value).data(\'model\'))) {\n                        found = true;\n                    }\n                    return !found;\n                });\n\n                $(this).removeClass(\'layer-hover\');\n\n                if (found && selectedLayers.length > 0) {\n                    $(this).addClass(\'selected\');\n                } else {\n                    $(this).removeClass(\'selected\');\n\n                }\n            });\n        },\n\n        remove: function () {\n            this.removeEventListeners();\n            return Backbone.View.prototype.remove.call(this);\n        }\n    });\n\n    return InspectFontFaceView;\n});\n\n//# sourceURL=/lib/graphite-api/public/js/ccweb.files.extract/views/inspect/InspectFontFaceView.js");

