eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4 */\n/*global define: true, graphite: true, clearTimeout: true, setTimeout: true, clearTimeout: true*/\n\ndefine(\'ext/views/CSSHintView\',[\n    \"jquery\",\n    \"underscore\",\n    \"backbone\",\n    \"serverAPI\",\n    \"ccweb.files.extract/utils/SpriteSheetUtils\",\n    \"ccweb.files.extract/models/LayerModelMap\",\n    \"ccweb.files.extract/utils/MeasurementUtil\",\n    \"ccweb.files.extract/controllers/SelectionController\",\n    \"ext/Constants\",\n    \"text!ext/views/templates/code-hint-item-view.html\",\n    \"ext/strings\"\n], function ($, _, Backbone, serverAPI, SpriteSheetUtils, LayerModelMap,\n        MeasurementUtil, SelectionController, Constants, CodeHintItemViewTemplate,\n        Strings) {\n    \"use strict\";\n\n    var CSSHintView = Backbone.View.extend({\n\n        createTemplate: function (templateId, data) {\n            var srcHTML = $(CodeHintItemViewTemplate).filter(templateId).html(),\n                template = _.template(srcHTML);\n\n            return template(data).trim();\n        },\n\n        initialize: function () {\n            var self = this,\n                type = this.model.get(\"type\");\n\n            this.$el.data(\"type\", type);\n            this.$el.data(\"hint\", this.model.get(\"hint\"));\n            this.$el.data(\"property\", this.model.get(\"property\"));\n            this.$el.data(\"toHint\", function (context) {\n                return self.model.toHint(context);\n            });\n            this.$el.data(\"isBulkHint\", this.model.isBulkHint());\n            \n            this.render();\n\n            if (type === Constants.Hint.IMAGE) {\n                var layerModel = this.model.getLayers()[0],\n                    elem = LayerModelMap.getLayerInfoForId(layerModel.get(\"layerId\")).item,\n                    canvas = elem[0];\n                \n                this.model.set(\"canvas\", canvas);\n                \n                // FIXME can\'t access view or model from PSDCodeHints, embed\n                // toDataURL as jQuery data property\n                this.$el.data(\"toDataURL\", function (type) {\n                    return self.model.toDataURL(type);\n                });\n            }\n        },\n\n        render: function () {\n            var self = this,\n                type = this.model.get(\"type\"),\n                json = this.model.toJSON(),\n                merged = this.model.isMergeLayers();\n\n            this.$el.html(this.createTemplate(\"#hint-view\", json)).addClass(\"parfait-hint-item\");\n\n            if (merged) {\n                this.$el.find(\".parfait-hint-icon\").addClass(\"merged\");\n            }\n\n            if (type === Constants.Hint.COLOR) {\n                this.$el.find(\".parfait-hint-color-chip\").css(\"background-color\", json.value);\n            } else if (type === Constants.Hint.GRADIENT) {\n                var gradientValue = json.value;\n\n                // Find the w3c, unprefixed value\n                if (Array.isArray(json.value)) {\n                    // Use the last value by default\n                    gradientValue = gradientValue[gradientValue.length - 1].value;\n\n                    json.value.some(function (elem) {\n                        if (elem.value.indexOf(\"-\") < 0) {\n                            gradientValue = elem.value;\n                            return true;\n                        }\n                    });\n                }\n\n                var gradient = Constants.RegExp.STRIP_VENDOR_PREFIX.exec(gradientValue)[2];\n                this.$el.find(\".parfait-hint-gradient-chip\").css(\"background-image\", gradient);\n            } else if (type === Constants.Hint.IMAGE) {\n                // Draw async to compute width. This can\'t be done for a DOM\n                // fragment without a parent.\n                window.setTimeout(function () {\n                    self.drawSpriteSheet();\n                });\n            }\n\n            return this;\n        },\n\n        _finishMergedThumbnail: function (data, className, err) {\n            var $spinner = this.$el.find(\".spinner\");\n\n            // Add checker board\n            this.$el.find(\".parfait-hint-thumbnail\").addClass(\"sprite_preview\");\n\n            // Remove spinner, add merged thumbnail\n            $spinner.removeClass(\"spinner spin large\")\n                .addClass(\"parfait-hint-thumbnail-merged\");\n\n            if (data) {\n                $spinner.css(\"background-image\", \"url(\" + data + \")\");\n            } else if (className) {\n                $spinner.addClass(className);\n            }\n            \n            var $error = this.$el.find(\".parfait-hint-error\");\n            $error.text(err);\n            $error.addClass(\"show\");\n        },\n\n        drawSpriteSheet: function () {\n            var self = this;\n            \n            if (this.model.isMergeLayers()) {\n                var psdModel = SelectionController.getPSDModel(),\n                    isLocal = !!psdModel.get(\"localContent\"),\n                    layers = this.model.getLayers(),\n                    $spinner = this.$el.find(\".spinner\");\n\n                if (isLocal) {\n                    this._finishMergedThumbnail(null, \"parfait-hint-offline\", Strings.STATIC_CONTENT_SERVICE_ERROR);\n                } else {\n                    // .parfait-hint-thumbnail { width: 120px; height: 90px; }\n                    var options = { scale: 1.0 },\n                        bounds = MeasurementUtil.calcLayerGroupBounds(layers, psdModel),\n                        width = bounds.right - bounds.left,\n                        height = bounds.bottom - bounds.top,\n                        size = Math.max(width, height),\n                        maxSize = (size === width) ? 120 : 90,\n                        scale = maxSize / size;\n\n                    // Cap scaling at 1.0\n                    options.scale = Math.min(scale, options.scale);\n\n                    serverAPI.getDerivedAsset(psdModel, layers, \"png-8\", options).done(function (data) {\n                        self._finishMergedThumbnail(data);\n                    }).fail(function (err) {\n                        self._finishMergedThumbnail(null, \"parfait-hint-offline\", Strings.OFFLINE_ERROR);\n                    });\n                }\n            } else {\n                SpriteSheetUtils.renderSpriteModel(this, this.model.get(\"layerModel\"), \".parfait-hint-thumbnail\", \'.sprite_preview\', false);\n            }\n        }\n    });\n\n    return CSSHintView;\n\n});\n\n//# sourceURL=/./views/CSSHintView.js");

