eval("/*\n * ADOBE CONFIDENTIAL\n *\n * Copyright (c) 2014 Adobe Systems Incorporated. All rights reserved.\n *\n * NOTICE:  All information contained herein is, and remains\n * the property of Adobe Systems Incorporated and its suppliers,\n * if any.  The intellectual and technical concepts contained\n * herein are proprietary to Adobe Systems Incorporated and its\n * suppliers and are protected by trade secret or copyright law.\n * Dissemination of this information or reproduction of this material\n * is strictly forbidden unless prior written permission is obtained\n * from Adobe Systems Incorporated.\n */\n\n/*jslint vars: true, plusplus: true, devel: true, nomen: true, indent: 4, maxerr: 50 */\n/*global brackets, define, Mustache*/\n\ndefine(\'ext/ParfaitPopup\',[\n    \"jquery\",\n    \"ext/command/Commands\",\n    \"ext/Constants\",\n    \"ext/models/ExtensionMetadata\",\n    \"ext/models/BracketsPreferencesModel\",\n    \"text!ext/html/parfait-toolbar.html\",\n    \"text!ext/html/parfait-popup.html\",\n    \"ext/strings\"\n], function ($, Commands, Constants, ExtensionMetadata, BracketsPreferencesModel,\n    parfaitToolbarHtml, firstLaunchPopupHtml, Strings) {\n    \"use strict\";\n    \n    var MainViewManager     = brackets.getModule(\"view/MainViewManager\");\n    \n    var prefs = BracketsPreferencesModel.get(\"preferenceSystem\"),\n        $toolbarIcon = null,\n        cmdTogglePanel;\n    \n    function closeCallout() {\n        var $callout = $(\"#psdl-firstlaunch-popup\");\n        \n        // Don\'t re-show toolbar callout on next launch (regardless of how it was dismissed)\n        prefs.set(BracketsPreferencesModel.HIDE_FIRST_LAUNCH_TOOLBAR_TOOLTIP, true);\n        \n        // Don\'t animate if already closed - we\'d never be triggered to remove \"animateClose\", preventing reopen later. Normally,\n        // AnimationUtils\' .is(\":hidden\") check serves this purpose, but it breaks the animation in this case (see below).\n        if (!$callout.hasClass(\"animateOpen\")) {\n            return;\n        }\n        \n        // Animate out\n        $callout.removeClass(\"animateOpen\");\n        $callout\n            .addClass(\"animateClose\")\n            .one(\"webkitTransitionEnd\", function () {\n                // Normally we\'d use AnimationUtils for this, but due to an apparent Chrome bug, calling .is(\":hidden\")\n                // causes the animation not to play (even though it returns false and that early-exit branch isn\'t taken).\n                $callout.removeClass(\"animateClose\");\n                $callout.remove();\n            });\n    }\n    \n    function _handleCalloutClick() {\n        closeCallout();\n        MainViewManager.focusActivePane();\n    }\n    \n    function _handleIconClick() {\n        closeCallout();\n        Commands.execute(Commands.TOGGLE_PANEL);\n    }\n    \n    function _updateIconState() {\n        var active = cmdTogglePanel.getChecked();\n        if (active) {\n            $(\"#toolbar-parfait\").addClass(\"active\");\n        } else {\n            $(\"#toolbar-parfait\").removeClass(\"active\");\n        }\n    }\n    \n    function _showFirstLaunchTooltip() {\n        // Bail if we\'ve already shown the tooltip or if this is a bundled build\n        var hideTooltip = prefs.get(BracketsPreferencesModel.HIDE_FIRST_LAUNCH_TOOLBAR_TOOLTIP),\n            isBundledBuild = ExtensionMetadata.isBundledBuild();\n\n        if (hideTooltip || isBundledBuild) {\n            return;\n        }\n        \n        // Add first-launch callout next to toolbar icon (hidden)\n        var NOTCH_TIP_OFFSET = 6,\n            popupTop = $toolbarIcon.offset().top - NOTCH_TIP_OFFSET;\n        var $firstLaunchPopup = $(Mustache.render(firstLaunchPopupHtml, Strings));\n        $firstLaunchPopup.appendTo(\"body\").hide()\n                         .css(\"top\", popupTop)\n                         .find(\"button\").click(_handleCalloutClick);\n        $firstLaunchPopup.show();\n        \n        // Animate in\n        // Must wait a cycle for the \"display: none\" to drop out before CSS transitions will work\n        setTimeout(function () {\n            $(\"#psdl-firstlaunch-popup\").addClass(\"animateOpen\");\n        }, 0);\n    }\n    \n    function _initParfaitControlsPopup() {\n        $toolbarIcon = $(Mustache.render(parfaitToolbarHtml, Strings));\n        $toolbarIcon.appendTo(\"#main-toolbar > .buttons\");\n        $toolbarIcon.on(\"click\", _handleIconClick);\n        \n        cmdTogglePanel = Commands.get(Commands.TOGGLE_PANEL);\n        $(cmdTogglePanel).on(\"checkedStateChange\", _updateIconState);\n        \n        // Initialize first-launch callout based on preference\n        var firstLaunchTooltipPref = prefs.getPreference(BracketsPreferencesModel.HIDE_FIRST_LAUNCH_TOOLBAR_TOOLTIP);\n        firstLaunchTooltipPref.on(\"change\", _showFirstLaunchTooltip);\n        _showFirstLaunchTooltip();\n    }\n\n    return {\n        initParfaitControlsPopup: _initParfaitControlsPopup\n    };\n});\n//# sourceURL=/./ParfaitPopup.js");

